<template>
  <div :class="className" class="chart" :id="id" :style="{width:width,height:height,width:'100%'}"></div>
</template>
<script>
import echarts from "echarts";
import resize from "./mixins/resize";
import { dateCalcRound } from "@/util";
export default {
  mixins: [resize],
  props: {
    className: {
      type: String,
      default: "chart"
    },
    id: {
      type: String,
      default: "chart"
    },
    width: {
      type: String
    },
    height: {
      type: String,
      default: "300px"
    },
    fixNum: {
      type: Number,
      default: 2
    },
    dataLists: {
      type: [Array, Object],
      default() {
        return [];
      }
    },
    title: {
      type: String,
      default: ""
    },
    attrs: {
      type: Object,
      default() {
        return {
          max: "max",
          min: "min",
          value: "value",
          unit: "unit"
        };
      }
    }
  },
  data() {
    return {
      chart: null,
      xAxis: []
    };
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  watch: {
    dataLists(obj) {
      this.initChart(
        obj[this.attrs.min],
        obj[this.attrs.max],
        [{ name: obj[this.attrs.unit], value: obj[this.attrs.value] }],
        this.fixNum
      );
    }
  },
  methods: {
    initChart(minValue, maxValue, series, fixNum) {
      if (this.chart) {
        this.chart.dispose();
        this.chart = null;
      }
      this.chart = echarts.init(document.getElementById(this.id), "shine");

      this.chart.setOption({
        title: {
          text: this.title
        },
        tooltip: {
          formatter(params, ticket, callback) {
            return (
              params.seriesName +
              ":" +
              params.value.toFixed(fixNum * 2) +
              params.name
            );
          }
        },
        toolbox: {
          show: true,
          feature: {
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        series: [
          {
            name: this.title,
            type: "gauge",
            z: 3,
            min: minValue,
            max: maxValue,
            splitNumber: 11,
            radius: "100%",
            axisLine: {
              // 
              lineStyle: {
                // lineStyle
                width: 10
              }
            },
            axisTick: {
              // 
              length: 15, // length
              lineStyle: {
                // lineStyle
                color: "auto"
              }
            },
            splitLine: {
              // 
              length: 20, // length
              lineStyle: {
                // lineStylelineStyle
                color: "auto"
              }
            },
            axisLabel: {
              //   backgroundColor: "auto",
              //   borderRadius: 2,
              //   color: "#eee",
              //   padding: 3,
              //   textShadowBlur: 2,
              //   textShadowOffsetX: 1,
              //   textShadowOffsetY: 1,
              //   textShadowColor: "#222",
              formatter(value) {
                if (maxValue < 10) {
                  return value.toFixed(fixNum);
                } else {
                  return value.toFixed(0);
                }
              }
            },
            title: {
              // TEXTSTYLE
              fontWeight: "bolder",
              fontSize: 14,
              fontStyle: "italic"
            },
            detail: {
              // TEXTSTYLE
              formatter: function(value) {
                return value.toFixed(fixNum);
              },
              fontSize: 14,
              fontWeight: "bolder",
              borderRadius: 3,
              backgroundColor: "#444",
              borderColor: "#aaa",
              shadowBlur: 5,
              shadowColor: "#333",
              shadowOffsetX: 0,
              shadowOffsetY: 3,
              borderWidth: 2,
              textBorderColor: "#000",
              textBorderWidth: 2,
              textShadowBlur: 2,
              textShadowColor: "#fff",
              textShadowOffsetX: 0,
              textShadowOffsetY: 0,
              fontFamily: "Arial",
              width: "auto",
              padding: [7, 10],
              color: "#eee",
              //   offsetCenter: [-10, "40%"], //
              rich: {}
            },
            data: series
          }
        ]
      });
    }
  },
  mounted() {}
};
</script>



