// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from './util/vueutil'
import App from './App'
import router, {
    platRoute
} from './router'
import ElementUi from 'element-ui' //ui
import 'element-ui/lib/theme-chalk/index.css'
import './assets/styles/reset.css' //
import './assets/styles/setElementUi.css' //
import './assets/styles/style.css' //
import store from './store'
import 'babel-polyfill' //ie

//ueditor
import '../static/scripts/utf8-jsp/ueditor.config.js'
import '../static/scripts/utf8-jsp/ueditor.all.js'
import '../static/scripts/utf8-jsp/lang/zh-cn/zh-cn.js'
import '../static/scripts/utf8-jsp/ueditor.parse.min.js'

import {
    getCookie,
    delCookie
} from "./util/util";


Vue.config.productionTip = false;
Vue.use(ElementUi);

const whiteList = ['/login'] // 

router.beforeEach((to, from, next) => {
    if (getCookie("account")) {
        if (to.path === '/login') {
            next({
                path: from.path
            })
        } else if (platRoute.some(item => item.path === to.path)) {
            store.commit("setLoading", 2) //isloadingtrue
            next();
        } else {
            next("/");
        }
    } else {
        if (whiteList.indexOf(to.path) > -1) {
            next();
        } else {
            next("/login")
        }
    }
    // if (true) {
    //     next();
    // } else {
    //     // from.name ? next({ name: from.name }) :
    //     next('/')
    // }
})
router.afterEach((to, from) => {
    store.commit("setLoading", 0); //isloadingfalse
});

/* eslint-disable no-new */
let vm = new Vue({
    el: '#app',
    store,
    router,
    template: '<App/>',
    components: {
        App
    }
})

export default vm;


