<template>
  <!--
  :max-height="treeMaxHeight"-->
  <el-table :data="data" border style="width: 100%" class="base-treeGrid" v-loading.lock="isLoading" :height="treeGridHeight+'px'" @row-click="rowClick" ref="baseTreeGrid" @selection-change="selectionChange" :row-style="showTr" :default-sort="defaultSort" :row-class-name="rowClassName" @select="selectCheck" @select-all="selectCheck">
    <el-table-column type="selection" v-if="showSelection" align="center"></el-table-column>
    <el-table-column type="index" width="70px" label="" align="center"></el-table-column>
    <el-table-column v-for="(column, index) in columns" :key="column.prop" :label="column.label" :min-width="column.width" :align="index == 0? 'left':column.align" header-align="center">
      <template slot-scope="scope">
        <div v-if="spaceIconShow(index)" v-for="(space, levelIndex) in scope.row._level" :key="levelIndex" class="ms-tree-space"></div>
        <i :class="scope.row._expanded?'el-icon-caret-bottom':'el-icon-caret-right'" v-if="toggleIconShow(index,scope.row,scope)" @click="toggle(scope.$index,scope.row)"></i>
        <div v-else-if="index===0" class="ms-tree-space"></div>
        {{customDisplay(column, scope)}}
      </template>
    </el-table-column>
    <slot name="operation">
      <el-table-column label="" v-if="treeType === 'normal'" width="115" align="left" header-align="center">
        <template slot-scope="scope">
          <el-button @click="editModule(scope.row,scope)" size="small">
            
          </el-button>
          <el-button size="small" @click="deleteModule(scope.row)" v-show="!scope.row.state">
            
          </el-button>
        </template>
      </el-table-column>
    </slot>

  </el-table>
</template>
<script>
import Utils from "@/util/dataTranslate";

export default {
  name: "tree-grid",
  props: {
    rowClassName: {
      type: [Function, String],
      default: ""
    },
    //
    treeMaxHeight: {
      default: ""
    },
    //
    sort: {
      default: "displayOrder"
    },
    //
    load: {
      default: false
    },
    //
    setTreeHeight: {
      default: false
    },
    //CheckBox
    showSelection: {
      type: Boolean,
      default: function() {
        return false;
      }
    },
    // 
    treeStructure: {
      type: Boolean,
      default: function() {
        return true;
      }
    },
    // 
    columns: {
      type: Array,
      default: function() {
        return [];
      }
    },
    // 
    dataSource: {
      type: Array,
      default: function() {
        return [];
      }
    },

    // url
    requestUrl: {
      type: String,
      default: function() {
        return "";
      }
    },

    // 
    treeType: {
      type: String,
      default: function() {
        return "normal";
      }
    },
    // 
    defaultExpandAll: {
      type: Boolean,
      default: function() {
        return true;
      }
    }
  },
  data() {
    return {
      isLoading: false,
      treeGridHeight: "", //
      dataTmp: [],
      defaultSort: {
        prop: this.sort
      }
    };
  },
  computed: {
    // 
    data: function() {
      let me = this;
      if (me.load) {
        //
        return this.dataTmp;
      } else if (me.treeStructure) {
        //
        let data = Utils.MSDataTransfer.DataTransfer.treeToArray(
          me.dataSource,
          null,
          null,
          me.defaultExpandAll
        );
        return data;
      }
      return me.dataSource; //
    }
  },
  methods: {
    openLoading() {
      this.isLoading = true;
    },
    closeLoading() {
      this.isLoading = false;
    },
    //
    loadTree(arr, key, value) {
      arr.forEach((item, index) => {
        if (item[key] === value) {
          if (item.children && item.children.length > 0) {
            for (let i = 0; i < item.children.length; i++) {
              let val = item.children[i];
              arr.splice(index + 1 + i, 0, val);
            }
          }
        }
      });
    },
    // 
    showTr: function(element, index) {
      let row = element.row;
      let show = row._parent
        ? row._parent._expanded && row._parent._show
        : true;
      row._show = show;
      return show ? "" : "display:none;";
    },
    // 
    toggle: function(trIndex, row) {
      let me = this;
      let record = me.data[trIndex];
      record._expanded = !record._expanded;
      if (row.index === undefined && me.load) {
        me.loadTree(me.dataTmp, "id", row.id);
        row.index = 1;
      }
    },
    // 
    spaceIconShow(index) {
      let me = this;
      if (me.treeStructure && index === 0) {
        return true;
      }
      return false;
    },
    // 
    toggleIconShow(index, record) {
      let me = this;
      if (
        me.treeStructure &&
        index === 0 &&
        record.children &&
        record.children.length > 0
      ) {
        return true;
      }
      return false;
    },
    rowClick(row, event) {
      if (
        !(
          event.target.outerHTML.indexOf("<span") == 0 ||
          event.target.outerHTML.indexOf("<i") == 0 ||
          event.target.outerHTML.indexOf("<button") == 0
        )
      ) {
        this.$refs.baseTreeGrid.toggleRowSelection(row);
      }
    },
    selectionChange(selection) {
      this.$emit("selectionChange", selection);
    },
    //
    deleteModule(val) {
      this.$emit("deleteModule", val);
    },
    //
    editModule(val, va1) {
      let newV = {};
      for (var key in val) {
        if (!(typeof val[key] == "object" || typeof val[key] == "function")) {
          newV[key] = val[key];
        }
      }
      this.$emit("editModule", newV);
    },
    //
    selectCheck(selection) {},
    //
    setHeight() {
      if (this.setTreeHeight) {
        let me = this,
          timer = null;
        me.treeGridHeight = document.body.clientHeight - 200;
        window.onresize = function() {
          if (timer) {
            clearTimeout(timer);
          }
          timer = setTimeout(() => {
            me.treeGridHeight = document.body.clientHeight - 200;
          }, 200);
        };
      }
    },
    customDisplay(item, scope) {
      var temp = "";
      if (item.displace) {
        temp = item.displace[scope.row[item.prop]];
      } else {
        temp = scope.row[item.prop];
      }

      //
      if (
        item.format &&
        (typeof temp === "string" || typeof temp === "number")
      ) {
        try {
          temp = this.format(temp, item.format);
        } catch (e) {
          console.warn(e);
        }
      }
      return temp;
    }
  },
  mounted() {
    this.setHeight();
  },
  watch: {
    dataSource(n, o) {
      if (n !== o && this.load) {
        let me = this,
          dataSource;
        me.dataTmp = [];
        dataSource = me.dataSource ? me.dataSource : [];
        if (me.treeStructure) {
          let data = Utils.MSDataTransfer.DataTransfer.treeToArray(
            dataSource,
            null,
            null,
            me.defaultExpandAll
          );
          data.forEach(item => {
            if (item._level <= 1) {
              item.index = item._level == 0 ? 1 : undefined;
              me.dataTmp.push(item);
            }
          });
        }
      }
    }
  }
};
</script>
<style scoped>
.ms-tree-space {
  position: relative;
  top: 1px;
  display: inline-block;
  font-family: "Glyphicons Halflings";
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  width: 18px;
  height: 14px;
}
.base-treeGrid .el-icon-caret-right {
  width: 18px;
}
.ms-tree-space::before {
  content: "";
}

.base-treeGrid table td {
  line-height: 26px;
}
</style>



