<template>
    <div class="analysis-panel" :style="{width:width}">
        <h3 class="text-center">{{title}}</h3>
        <div class="analysis-panel-body">
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: ""
    },
    width: {
      type: String,
      default: "280px"
    }
  }
};
</script>


<style scoped>
.analysis-panel {
  border: 1px solid #78adfe;
}

.analysis-panel h3 {
  line-height: 30px;
  background-color: #13599f;
  color: #fff;
}
.analysis-panel .analysis-panel-body {
  padding: 10px;
}
</style>



