<template>
  <div :class="className" :id="id" :style="{height:height,width:width}"></div>
</template>

<script>
import echarts from "echarts";
import resize from "./mixins/resize";
import { dateCalcRound } from "@/util";

export default {
  mixins: [resize],
  props: {
    className: {
      type: String,
      default: "chart"
    },
    id: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "250px"
    },
    selectedNum: {
      type: Number,
      default: 5
    },
    label: {
      type: String,
      default: "name"
    },
    valueKey: {
      type: String,
      default: "dataValue"
    },
    unit: {
      type: String,
      default: "targetUnit"
    },
    title: {
      type: String
    },
    dataLists: {
      type: Array,
      default() {
        return [];
      }
    },
    legendDisplace: {
      type: Object
    },
    // title
    showTitle: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      chart: null,
      xAxis: []
    };
  },
  watch: {
    dataLists(lists) {
      if (this.dataLists.length === 0) {
        document.getElementById(this.id).innerHTML = this.chartNoData();
        return;
      }
      let legendData = [],
        seriesData = [],
        selected = {};
      this.$each(lists, (item, index) => {
        let trueName = this.legendDisplace
          ? this.legendDisplace[item[this.label]]
          : item[this.label];
        legendData.push(trueName);
        seriesData.push({
          name: trueName,
          value: item[this.valueKey]
        });
        selected[item.label] = index < this.selectedNum;
      });
      this.initChart(legendData, seriesData, selected);
    }
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    initChart(legendData, seriesData, selected) {
      if (this.chart) {
        this.chart.dispose();
        this.chart = null;
      }
      this.chart = echarts.init(document.getElementById(this.id), "shine");
      let unit = this.dataLists[0][this.unit];
      this.chart.setOption({
        title: {
          text: this.showTitle ? this.title : "",
          textStyle: {
            fontWeight: "lighter",
            fontSize: 14
          },
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c}" + unit + " ({d}%)"
        },
        legend: {
          type: "scroll",
          orient: "vertical",
          right: 10,
          top: 20,
          bottom: 20,
          data: legendData,

          selected: selected
        },
        series: [
          {
            name: this.title,
            type: "pie",
            radius: "55%",
            center: ["40%", "50%"],
            data: seriesData,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      });
    }
  },
  mounted() {}
};
</script>



