function getModuleId(arr, path) {
    for (let i = 0, len = arr.length; i < len; i++) {
        if (arr[i].path.indexOf(path) > -1) {
            return arr[i].id
        }
    }
}

/**
 *
 * @desc 
 * @return {String}
 */
function getExplore(ua) {
    var sys = {},
        s;
    ua = (ua || navigator.userAgent).toLowerCase();
    (s = ua.match(/rv:([\d.]+)\) like gecko/)) ?
    sys.ie = s[1]: (s = ua.match(/msie ([\d\.]+)/)) ?
        sys.ie = s[1] :
        (s = ua.match(/edge\/([\d\.]+)/)) ?
        sys.edge = s[1] :
        (s = ua.match(/firefox\/([\d\.]+)/)) ?
        sys.firefox = s[1] :
        (s = ua.match(/(?:opera|opr).([\d\.]+)/)) ?
        sys.opera = s[1] :
        (s = ua.match(/chrome\/([\d\.]+)/)) ?
        sys.chrome = s[1] :
        (s = ua.match(/version\/([\d\.]+).*safari/)) ?
        sys.safari = s[1] :
        0;
    // 
    if (sys.ie) {
        return ('IE: ' + sys.ie);
    }
    if (sys.edge) {
        return ('EDGE: ' + sys.edge);
    }
    if (sys.firefox) {
        return (': ' + sys.firefox);
    }
    if (sys.chrome) {
        return (': ' + sys.chrome);
    }
    if (sys.opera) {
        return (': ' + sys.opera);
    }
    if (sys.safari) {
        return ('Safari: ' + sys.safari);
    }
    return 'Unkonwn'
}

/**/
function sortRule(list, sortStr) {
    for (let i = 0; i < list.length; i++) {
        for (let j = i + 1; j < list.length; j++) {
            if (list[i][sortStr] >= list[j][sortStr]) {
                let tempLi = list[j];
                list[j] = list[i];
                list[i] = tempLi;
            }
        }
    }
    return list;
}
/**
 * 
 * @param {arr} Array 
 * @param {name} String 
 * @param {value} String 
 * @param {displace} Object name
 * @return Array [{name: "a",value: 3}...]
 * 
 */
function reduceById(arr, name, value, unit, displace) {
    let indexTotal = [];
    let target = [];
    each(arr, item => {
        let disName = item[name];
        if (displace) {
            disName = displace[disName] || item[name];
        }
        let index = indexTotal.indexOf(disName);
        if (index === -1) {
            indexTotal.push(disName);
            target.push({
                id: item[name],
                name: disName,
                value: item[value],
                targetUnit: item[unit]
            });
        } else {
            target[index].value += item[value];
        }
    });
    return target;
}

// 
function formatTime(date, format) {
    format = format || "YYYY-MM-DD";
    var time = new Date(date);
    var year = time.getFullYear() || "1970";
    var month = (time.getMonth() + 1) || 1;
    var day = time.getDate() || 1;
    var hour = time.getHours() || 0;
    var minute = time.getMinutes() || 0;
    var second = time.getSeconds() || 0;
    format = format.replace("YYYY", year);
    format = format.replace("MM", month < 10 ? "0" + month : month);
    format = format.replace("DD", day < 10 ? "0" + day : day);
    format = format.replace("HH", hour < 10 ? "0" + hour : hour);
    format = format.replace("mm", minute < 10 ? "0" + minute : minute);
    format = format.replace("ss", second < 10 ? "0" + second : second);
    return format;
}

/**
 * 
 * @param {date} [String,Date] 
 * @param {identify} Number 
 * @param {index} Number 12345678
 * @param {format}  String  YYYY-MM-DD HH:mm:ss
 * */
function calcTime(date, identify, index, format) {
    let time = new Date(date);
    let orginDate = time.getDate();
    let splitTime = ["", "Hours", "", "Date", "", "Month", "", "", "FullYear"];
    let timeType = ["", "YYYY-MM-DD HH:00:00", "", "YYYY-MM-DD", "", "YYYY-MM", "", "", "YYYY"];
    if (isNaN(time.getTime()) || index > 8 || splitTime[index] === "") {
        console.warn("(" + date + "," + identify + "," + index + ")");
    }
    // 
    if (index === 5) {
        let timeDate = new Date(time.getFullYear(), (time.getMonth() + (1 + identify)), 0).getDate();
        if (orginDate > timeDate) {
            time.setDate(timeDate);
        }
    } else if (index === 8) {
        let timeDate = new Date(time.getFullYear() + identify, time.getMonth() + 1, 0).getDate();
        if (orginDate > timeDate) {
            time.setDate(timeDate);
        }
    }
    time["set" + splitTime[index]](
        time["get" + splitTime[index]]() + identify
    );
    return formatTime(time, format || timeType[index]);
}

/**
 * ()
 * @param {date} 
 * @param {index} 12345678
 * */
function cycleTime(date, index) {
    let time = new Date(date);
    let splitTime = ["", "Hours", "", "Date", "", "Month", "", "", "FullYear"];
    let timeType = ["", "", "", "YYYY-MM-DD", "", "YYYY-MM-01", "", "", "YYYY-01-01"];
    if (isNaN(time.getTime()) || timeType[index] === "") {
        console.warn("");
    }
    let startTime = formatTime(time, timeType[index]);
    let endTime = calcTime(calcTime(time, 1, index), -1, 3);
    return [startTime, endTime];
}

/**
 * 
 * @param array 
 * @param dataField dataValue
 * @param displayField displayPrecisions
 */
function getFixedDisplayData(array, dataField, displayField) {
    if (!dataField || dataField == "") {
        dataField = "dataValue";
    }
    if (!displayField || displayField == "") {
        displayField = "displayPrecisions";
    }
    if (array instanceof Array) {
        for (let i = 0; i < array.length; i++) {
            if (!isNaN(array[i][dataField])) {
                array[i][dataField] = parseFloat(array[i][dataField]);
            }
            if (!isNaN(displayField)) {
                array[i][dataField] = array[i][dataField].toFixed(parseFloat(displayField));
            } else {
                array[i][dataField] = array[i][dataField].toFixed(
                        array[i][displayField]
                    ) ?
                    array[i][dataField].toFixed(array[i][displayField]) :
                    array[i][dataField];
            }
        }
    } else if (array instanceof Object) {
        array[dataField] = array[dataField].toFixed(array[displayField]);
    } else if (!isNaN(array)) {
        if (!isNaN(dataField)) {
            array = array.toFixed(dataField);
        } else if (!isNaN(displayField)) {
            array = array.toFixed(displayField);
        } else {
            array = array.toFixed(2);
        }
    }
    return array;
}

