<template>
  <div class="panel" :style="{width:width,margin: margin,padding: padValue, 'min-width': minWidth}">
    <slot name="head">
      <div class="panel-head" v-if="!noHead">
        <h4 v-if="icon||title" class="panel-title">
          <i v-if="icon" :class="icon"></i>{{title}}</h4>
        <div class="panel-search">
          <slot name="search"></slot>
        </div>
      </div>
    </slot>
    <div class="panel-body clearfix" :style="{height:height,'max-height':maxHeight,padding: padContValue,'min-height':minHeight,'border': border?'1px solid #78adfe':false, overflow:overflow}" v-loading="isLoading">
      <slot></slot>
    </div>
    <div class="panel-footer">
      <slot name="footer"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String
    },
    icon: {
      type: String
    },
    width: {
      type: String,
      default: "100%"
    },
    minWidth: {
      type: String
    },
    border: {
      type: Boolean,
      default: true
    },
    height: {
      type: String
    },
    maxHeight: {
      type: String
    },
    minHeight: {
      type: String
      // default: "50px"
    },
    padding: {
      type: String,
      default: "" // trbl lrtbpadding
    },
    // padding: {
    //   type: String,
    //   default: "10px 0"
    // },
    paddingContent: {
      type: String,
      default: "" // trbl lrtbpadding
    },
    margin: {
      type: String,
      default: "0"
    },
    overflow: {
      type: String,
      default: "visible"
    },
    noHead: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isLoading: false
    };
  },
  computed: {
    // 
    padValue() {
      return this.setPad(this.padding);
    },
    padContValue() {
      return this.setPad(this.paddingContent);
    }
  },
  methods: {
    setPad(padV) {
      return ["t", "r", "b", "l"]
        .map(item => {
          if (padV.indexOf(item) === -1) {
            return 0;
          } else {
            return "10px";
          }
        })
        .join(" ");
    },
    openLoading() {
      this.isLoading = true;
    },
    closeLoading() {
      this.isLoading = false;
    }
  }
};
</script>

<style scoped>
.panel {
  /* margin: 0 10px; */
  float: left;
}
.panel-head {
  display: flex;
  align-items: flex-end;
  border-bottom: 3px solid #d5e8ff;
}

.panel-title {
  font-size: 16px;
  border-bottom: 3px solid #569cf1;
  margin-bottom: -3px;
  padding-right: 5px;
  width: auto;
}
.panel-title i {
  margin-right: 5px;
  font-weight: bold;
}
.panel-search {
  flex: 1;
  text-align: right;
  margin-bottom: 3px;
}
.panel-head + .panel-body {
  margin: 3px 0 0;
}
/* .panel-body { */
/* padding: 15px 10px; */
/* overflow: auto; */
/* } */
</style>



