<template>
  <el-radio-group v-if="optionLists.length > 0 && optionLists.length < maxNum" v-model="selected" @change="changeHandle">111
    <el-radio-button v-for="(item, index) in optionLists" :key="index" :label="item[valueKey]">{{item[label]}}</el-radio-button>
  </el-radio-group>
  <div v-else class="rs_wrap" :width="width">
    <el-button @click="lastHandle">
      <i class="el-icon-arrow-left"></i>
    </el-button>
    <el-select class="rs_select" v-model="selected" filterable placeholder="" @change="changeHandle">
      <el-option v-for="(item, index) in optionLists" :key="index" :label="item[label]" :value="item[valueKey]">
      </el-option>
    </el-select>
    <el-button @click="nextHandle">
      <i class="el-icon-arrow-right"></i>
    </el-button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: [String, Number]
    },
    // maxNum
    maxNum: {
      type: Number,
      default: 5
    },
    optionLists: {
      type: Array,
      default() {
        return [];
      }
    },
    label: {
      type: String,
      default: "name"
    },
    valueKey: {
      type: String,
      default: "id"
    },
    width: {
      type: String,
      width: "auto"
    }
  },
  data() {
    return {
      selected: {
        type: [String, Number]
      }
    };
  },
  watch: {
    optionLists(lists) {
      if (lists.length === 0) {
        this.$emit("input", "");
      } else if (this.value !== "") {
        this.selected = this.value;
      } else if (lists.length > 0) {
        this.$emit("input", lists[0][this.valueKey]);
        // this.changeHandle(this.selected);
      }
    },
    value() {
      if (this.value !== this.selected) {
        this.selected = this.value;
      }
    }
  },
  methods: {
    changeHandle(value) {
      this.$emit("input", value);
      this.$emit("change", value);
    },
    lastHandle() {
      let index = this.findNode(this.selected);
      if (index > 0) {
        this.selected = this.optionLists[index - 1][this.valueKey];
        this.changeHandle(this.selected);
      }
    },
    nextHandle() {
      let index = this.findNode(this.selected);
      if (index < this.optionLists.length - 1) {
        this.selected = this.optionLists[index + 1][this.valueKey];
        this.changeHandle(this.selected);
      }
    },
    findNode(value) {
      let temp = "";
      this.$each(this.optionLists, (item, index) => {
        if (item[this.valueKey] === value) {
          temp = index;
          return false;
        }
      });
      return temp;
    }
  },
  mounted() {
    if (this.optionLists.length > 0 && this.value !== 0 && !this.value) {
      this.$emit("input", this.optionLists[0][this.valueKey]);
      this.selected = this.value;
    } else {
      this.selected = this.value;
    }
  }
};
</script>
<style scoped>
.rs_wrap {
  display: inline-block;
}
.rs-no-data {
  padding: 5px 10px;
}
.rs_select {
  width: 150px;
}
</style>



