/**
 * 
 * this.$store.state.targetConstant.type
 * this.$store.commit("setType", obj)
 */
const targetConstant = {
  /**
   * 
   */
  state: {
    // type
    type: {},
    targetCycle: { hour: 1, week: 2, day: 3, week: 4, month: 5, quater: 6, halfYear: 7, year: 8 },
    /**
     * 
     * harvest:,energyConsum:,unitProductEnergyConsum:,saveEnergy:,
     * output:,unitProductEnergyCost:,energyCost:,energyRate: 
     */
    targetType: {
      harvest: 4,
      energyConsum: 1,
      unitProductEnergyConsum: 2,
      saveEnergy: 75,
      output: 6,
      unitProductEnergyCost: 17,
      energyCost: 30,
      energyPrice: 89,
      energyRate: 109
    },
    /**
     * 
     */
    targetContentType: { energy: 1 },
    /**
     * 
     * compreEnergyConsum:
     */
    energy: { compreEnergyConsum: 42, electric: 35 },
    /**
     * 
     */
    targetSubordinate: { workGroup: 1, equip: 2, product: 3 },
    orgId: { enterprise: 35 },
    /**
     * 
     */
    equipRunStatus: { stop: 1, standby: 2, run: 3 },
  },
  /**
   * 
   */
  mutations: {
    setType(state, payload) {
      state.type = payload;
    }
  },
  actions: {}

}
export default targetConstant


