<template>
  <div>
    <el-transfer :class="'transfer_' + size" :data="chooseData" v-model="accreditedList" :filterable="filterable" :render-content="renderContent" :left-default-checked="leftDefaultChecked" :right-default-checked="rightDefaultChecked" :titles="titles" :props="{
          key: myKey,
          label: myLabel,
          disabled: myDisabled
        }" :button-texts="buttonTexts" :format="{
        noChecked: '${total}',
        hasChecked: '${checked}/${total}'
        }" @change="handleChange" :footer-format="{ noChecked: ' ${total} ', hasChecked: ' ${checked}${total}' }">
      <!--<el-button class="transfer-footer" slot="left-footer" size="small"></el-button>-->
      <!--<el-button class="transfer-footer" slot="right-footer" size="small"></el-button>-->
    </el-transfer>
  </div>
</template>

<script>
import { varType } from "../../util/util.js";

export default {
  name: "myTransfer",
  components: {},
  props: {
    rend: {
      type: Function
    },
    size: {
      // xs,sm,md,lg
      type: String,
      default: "sm"
    },
    chooseList: {
      /**/
      type: Array,
      default: () => []
    },
    accredited: {
      /*,,1*/
      type: Array,
      default: () => []
    },
    filterable: {
      /**/
      type: Boolean,
      default: true
    },
    leftDefaultChecked: {
      /*,,1,*/
      type: Array,
      default: () => []
    },
    rightDefaultChecked: {
      /*,,1*/
      type: Array,
      default: () => []
    },
    titles: {
      /*,,*/
      type: Array,
      default: () => [":", ":"]
    },
    buttonTexts: {
      /*,,*/
      type: Array,
      default: () => ["", "", "", ""]
    },
    myKey: {
      /*id*/
      type: String,
      default: "id"
    },
    myLabel: {
      /*name*/
      type: String,
      default: "name"
    },
    myDisabled: {
      /*id*/
      type: String,
      default: "disabled"
    },
    saveClick: {
      type: Function
    },
    backClick: {
      type: Function
    },
    filterList: {
      type: Object,
      default() {
        return {};
      }
    },
    filterType: {
      type: String,
      default: "0"
    },
    emptyText: {
      default: ""
    },
    footerFormat: {
      type: Object
      //                default: () => {
      //                    noChecked: '${total}',
      //                        hasChecked:'${checked}/${total}'
      //                }
    }
  },
  data() {
    return {
      chooseData: [],
      accreditedList: []
    };
  },
  computed: {},
  created() {
    this.chooseData = this.chooseList;
    this.accreditedList = this.accredited;
  },
  mounted() {
    /**/
    let btns = this.$el.getElementsByClassName("el-transfer__buttons")[0];
    let btnChild = btns.children[1].children[0];
    btnChild.insertBefore(btnChild.children[1], btnChild.children[0]);
    /*i*/
    /** */
    for (let i = 2; i < this.buttonTexts.length; i++) {
      var item = this.buttonTexts[i];
      let tempBtn = document.createElement("button");
      tempBtn.innerHTML = `<span><i></i><span>${item}</span></span>`;
      tempBtn.classList.add(
        "el-button",
        "el-button--primary",
        "el-button--small"
      );
      if (i == 2 && this.saveClick) {
        tempBtn.addEventListener("click", () => {
          this.saveClick();
        });
      }
      if (i == 3 && this.backClick) {
        tempBtn.addEventListener("click", () => {
          this.backClick();
        });
      }
      btns.appendChild(tempBtn);
    }
  },
  methods: {
    handleChange(value, direction, movedKeys) {
      let tempObj = [];
      movedKeys.forEach((item, index) => {
        tempObj.push(this.chooseData[item - 1]);
      });
      this.$emit(
        "transfer" /*,transfer,,*/,
        {
          changed: tempObj /**/,
          value /**/,
          direction /*,leftright*/,
          movedKeys /**/
        }
      );
    },
    renderContent(h, option) {
      if (this.rend) {
        return this.rend(h, option);
      }
      return <span>{option[this.myLabel]}</span>;
    }
  },
  watch: {
    chooseList(val) {
      this.chooseData = val;
      if (this.chooseData.length == 0) {
        let nodes = this.$el.querySelectorAll(".el-transfer-panel__empty");
        nodes[1].innerHTML = this.emptyText;
        if (this.accreditedList.length == 0) {
          nodes[3].innerHTML = this.emptyText;
        }
        //                    for(var i=0,len=nodes.length;i<len;i++){
        //                        if(i%2 != 0){
        //                            nodes[i].innerHTML = this.emptyText
        //                        }
        //                  }
      } else {
        let nodes = this.$el.querySelectorAll(".el-transfer-panel__empty");
        nodes[1].innerHTML = "";
      }
    },
    accredited(val) {
      //
      if (
        val.every(function(item) {
          return typeof item === "string" || typeof item === "number";
        })
      ) {
        this.accreditedList = val;
      } else {
        let key = this.myKey;
        this.accreditedList = val.map(function(item) {
          return item[key];
        });
      }

      if (val.length == 0 && this.chooseList != 0) {
        this.$el.querySelectorAll(".el-transfer-panel__empty")[3].innerHTML =
          "";
      }
    },
    filterList: {
      handler(obj) {
        var ids = [],
          that = this;

        //
        that.chooseData = that.chooseList.filter(function(item) {
          let condition = false,
            isElement = -1;
          for (var attr in obj) {
            if (
              obj[attr] === "" ||
              varType(item[attr]) === "null" ||
              varType(item[attr]) === "undefined"
            ) {
              continue;
            } else if (varType(obj[attr]) === "array") {
              isElement = obj[attr].indexOf(item[attr]);
            } else if (
              varType(obj[attr]) === "string" ||
              varType(obj[attr]) === "number"
            ) {
              isElement = item[attr].indexOf(obj[attr]);
            }

            // 012
            if (that.filterType === "0") {
              condition = isElement === -1;
            } else if (that.filterType === "1") {
              condition =
                that.accreditedList.indexOf(item[that.myKey]) === -1 &&
                isElement === -1;
            } else if (that.filterType === "2") {
              condition =
                that.accreditedList.indexOf(item[that.myKey]) > -1 &&
                isElement === -1;
            }
            if (condition) {
              return false;
            }
          }
          return true;
        });
      }
      // deep: true
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only  -->
<style>
.bg-purple-light {
  background: #eeeeee;
}

.transfer_xs .el-transfer-panel__body {
  height: 200px;
}

.transfer_sm .el-transfer-panel__body {
  height: 350px;
}

.transfer_md .el-transfer-panel__body {
  height: 550px;
}

.transfer_lg .el-transfer-panel__body {
  height: 800px;
}

.userRightsManage_span_icon {
  margin-left: 10px;
}

.userRightsManage_span_title {
  margin-left: 10px;
}
</style>



