<template>
  <div class="inblock" :style="{width: width}">
    <el-radio-group v-show="visibleType" class="ts-radio" v-model="dateType" @change="changeHandle">
      <el-radio-button v-for="(item, index) in showTime" :key="index" :label="item">{{riadoList[item.toString()]?riadoList[item.toString()].label:""}}</el-radio-button>
    </el-radio-group>
    <el-button @click="lastTime">
      <i class="el-icon-arrow-left"></i>
    </el-button>
    <el-date-picker :type="riadoList[dateType.toString()]?riadoList[dateType.toString()].type:''" v-model="timeSelected" :clearable="false" @change="changeInput"></el-date-picker>
    <el-button @click="nextTime">
      <i class="el-icon-arrow-right"></i>
    </el-button>
  </div>
</template>
<script>
/**
 *
 * @event change 
 * @event inputChange 
 * @event radioChange 
 */
import { calcTime } from "../../util/util";
export default {
  props: {
    width: {
      type: String
    },
    value: {
      type: String,
      default() {
        return new Date();
      }
    },
    timeType: {
      type: Number
    },
    showTime: {
      type: Array,
      default() {
        return [3, 5, 8];
      }
    },
    // 
    visibleType: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      timeSelected: "",
      riadoList: {
        3: {
          label: "",
          type: "date",
          format: "YYYY-MM-DD"
        },
        5: {
          label: "",
          type: "month",
          format: "YYYY-MM-01"
        },
        8: {
          label: "",
          type: "year",
          format: "YYYY-01-01"
        }
      },
      dateType: this.timeType || this.showTime[0]
    };
  },
  watch: {
    value() {
      // 
      let time = this.$moment(
        this.value,
        this.riadoList[this.dateType.toString()].format
      );
      // 
      if (this.timeSelected !== time) {
        this.timeSelected = time;
        this.$emit("input", time);
      } else if (this.timeSelected === time && time !== this.value) {
        // 
        this.$emit("input", time);
      }
    },
    timeType() {
      // 
      if (this.dateType !== this.timeType) {
        this.dateType = this.timeType;
      }
    }
  },
  methods: {
    lastTime() {
      this.changeTime(-1);
    },
    nextTime() {
      this.changeTime(1);
    },
    changeTime(identify) {
      // 
      let time = calcTime(
        this.value,
        identify,
        this.dateType,
        this.riadoList[this.dateType.toString()].format
      );
      this.setData(time);
      this.$emit("inputChange", this.value, this.dateType);
    },
    changeHandle() {
      this.$emit("update:timeType", this.dateType);
      let value = this.$moment(
        this.value,
        this.riadoList[this.dateType.toString()].format
      );
      this.setData(value);
      this.$emit("radioChange", this.value, this.dateType);
    },
    changeInput(value) {
      value = this.$moment(
        value,
        this.riadoList[this.dateType.toString()].format
      );
      this.setData(value);
      this.$emit("inputChange", this.value, this.dateType);
    },
    setData(value) {
      this.$emit("input", value);
      this.$emit("change", value, this.dateType);
    }
  },
  mounted() {
    if (!this.value) {
      this.$emit(
        "input",
        this.$moment(
          new Date(),
          this.riadoList[this.dateType.toString()].format
        )
      );
      this.timeSelected = this.$moment(
        new Date(),
        this.riadoList[this.dateType.toString()].format
      );
    } else {
      this.timeSelected = this.value;
    }
  }
};
</script>

<style scoped>
.ts-radio {
  vertical-align: top;
}
.el-input {
  width: 120px;
}
</style>



