var success1 = function() {
        console.log('success');
    },
    er1 = function() {
        console.log('err');
    }

Vue.prototype.$alertMsg = function(msg, title, type = 'success', success, err) {
    if (success === undefined) {
        success = success1
    }
    if (err === undefined) {
        err = er1
    }
    this.$confirm(msg, title, {
        confirmButtonText: '',
        cancelButtonText: '',
        type: type
    }).then(() => {
        success()
    }).catch(() => {
        err()
    });
}
Vue.prototype.$showMsg = function(msg, title, type = 'success') {
    this.$notify({
        title: title,
        message: msg,
        type: type
    });
};
