<template>
  <el-upload :action="action" :on-success="handleChange" :on-remove="handleRemove" :file-list="mailFileList" :accept="accept">
    <el-button size="small" type="primary"></el-button>
  </el-upload>
</template>
<script>
/**
 * @author LJW 20171206
 * @description 
 *
 */
export default {
  props: {
    url: {
      type: String,
      default: "rest/mailService/upLoadFile/file"
    },
    fileList1: {
      type: String,
      default() {
        return "";
      }
    },
    accept: {
      type: String,
      default() {
        return "*";
      }
    }
  },
  data() {
    return {
      filename: "",
      mailFileList: []
    };
  },
  watch: {
    fileList1: function(newData, oldData) {
      this.filename = this.fileList1;
      this.mailFileList = this.fileList1
        .split(";")
        .filter(function(item, index) {
          if (item.trim() != "") {
            return item;
          }
        })
        .map(function(item, index) {
          return {
            name: item.substring(item.lastIndexOf("/") + 1),
            url: item
          };
        });
    }
  },
  computed: {
    action() {
      return "api/" + this.url;
    }
  },
  methods: {
    // 
    handleChange(response, file, fileList) {
      if (response.success) {
        this.filename += response.data + ";";
        this.$emit("change", response.data);
      } else {
        let len = this.fileList1.length;
        this.$message.error(response.msg);
        this.$emit("error", this.fileList1.slice("-" + len));
      }
    },
    // 
    handleRemove(file, fileList) {
      this.filename = this.filename.replace(curFile + ";", "");
      this.$emit("remove", file.response.data);
    },
    getFileName() {
      return this.filename;
    }
  }
};
</script>
<style>
</style>



