import Vue from 'vue'
import Vuex from 'vuex'
import app from './app'
import store from './store'
import getters from './getters'
import targetConstant from './targetConstant'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    app,
    targetConstant,
    store
  },
  getters
})



