<template>
  <div class="base-tree" :style="{height:height+'px'}">
    <!---->
    <el-tree v-if="load" :data="data" :props="props" :show-checkbox="showCheckbox" :default-expand-all="defaultExpand" :expand-on-click-node="expandOnClickNode" ref="loadTree" :node-key="defaultKeys.nodeKey" :check-strictly="checkStrictly" :default-expanded-keys="defaultKeys.expandedKeys" :default-checked-keys="defaultKeys.checkedKeys" @node-click="handleNodeClick" @check-change="checkChange" @current-change="currentChange" highlight-current :load="loadNode" :render-content="renderContent" @node-expand="nodeExpand">
    </el-tree>

    <el-tree v-else :props="props" ref="noLoadTree" :data="data" :show-checkbox="showCheckbox" highlight-current :filter-node-method="filterNodeMethod" :render-content="renderContent" node-key="id" :default-expanded-keys="defaultKeys.expandedKeys" :default-expand-all="defaultExpand" :check-strictly="checkStrictly" @node-click="handleNodeClick" @check-change="checkChange" @current-change="currentChange" @node-expand="nodeExpand" :expand-on-click-node="expandOnClickNode" :default-checked-keys="defaultKeys.checkedKeys">
      <!---->
      <!-- <el-tree :default-checked-keys="defaultKeys.checkedKeys"> -->
    </el-tree>
  </div>
</template>

<script>
/*
    * rend
    * rend(h, {node, data, store}) {//
     *
     *          let renderChild = function () {
     *               if(data.children){
     *                   return  <el-tag>{data.children.length}</el-tag>
     *               }
     *               return
     *           }
     *           fn qiye(data)
     *           if(data.){
     *
     *           return <span style="float:right;margin-right:20px">
     *                           {renderChild()}
     *                       </span>
     *                       qiye
     *                       return ''
     *           )
     *           return (
     *                   <span>
     *                       <span>{node.label}</span>
     *                       {qiye(node)}
     *                   </span>);
     *       },
     * */
import { deepJSONCopy } from "../../util/util";
export default {
  props: {
    value: {
      type: [String, Number]
    },
    //,jsxdom
    //            class:{
    //
    //            },
    rend: {
      type: Function
    },
    //
    load: {
      default: false
    },
    //
    setTreeHeight: {
      type: Boolean,
      default: false
    },
    //
    minusH: {
      default: 200
    },
    //
    showCheckbox: {
      type: Boolean,
      default: false
    },
    expandOnClickNode: {
      type: Boolean,
      default: true
    },
    //key value
    props: {
      type: Object,
      default() {
        return {
          children: "children",
          label: "name"
        };
      }
    },
    valueKey: {
      type: String,
      default: "id"
    },
    //
    defaultExpand: {
      type: Boolean,
      default: false
    },
    // 
    filterNodeMethod: {
      type: Function
    },
    //
    defaultKeys: {
      default: () => {
        let obj = { nodeKey: "id" };
        obj.expandedKeys = [];
        obj.checkedKeys = [];
        return obj;
      }
    },
    // 
    checkStrictly: {
      type: Boolean,
      defalut: false
    },
    dataLists: {
      type: Array
    }
  },
  data() {
    return {
      data: [],
      height: ""
    };
  },
  watch: {
    dataLists(val) {
      this.data = val;
    }
  },
  methods: {
    // 
    filter(val) {
      this.$refs.noLoadTree.filter(val);
    },
    setData(val) {
      this.data = val ? val : [];
    },
    //
    handleNodeClick(obj, node, component) {
      //obj,node,component: Node
      let msg = { obj, node, component };
      this.$emit("nodeClick", msg);
    },
    //
    checkChange(obj, nodeCheckd, childCheck) {
      // 
      // if (this.checkStrictly) {
      let data = this.value.split(",").filter(item => {
        return item !== "";
      });
      //
      if (nodeCheckd) {
        data.push(obj[this.valueKey]);
      } else {
        data = data.filter(item => {
          return parseInt(item) !== obj[this.valueKey];
        });
      }
      this.$emit("input", data.join(","));
      // } else {
      // console.log(obj, nodeCheckd, childCheck, obj[this.valueKey]);
      // }
      //obj,nodeCheckd,childCheck   data 
      let msg = { obj, nodeCheckd, childCheck };
      this.$emit("checkChange", msg);
    },
    //,
    currentChange(obj, node) {
      let msg = { obj, node };
      if (!this.showCheckbox) {
        this.$emit("input", obj[this.valueKey]);
      }
      this.$emit("currentChange", msg);
    },
    //
    nodeExpand(obj, node, component) {
      //obj,node,component: Node
      let msg = { obj, node, component };
      this.$emit("nodeExpand", msg);
    },
    //
    setHeight() {
      let me = this,
        timer = null;
      if (this.setTreeHeight) {
        me.height = document.body.clientHeight - me.minusH;
        window.onresize = function() {
          if (timer) {
            clearTimeout(timer);
          }
          timer = setTimeout(() => {
            me.height = document.body.clientHeight - me.minusH;
            me.$emit("treeHeight", me.height);
          }, 200);
        };
      }
    },
    loadNode(node, resolve) {
      //
      let tier = []; // ()
      if (node.level == 0) {
        resolve(this.data); //
        return;
      }
      var findParent = function(obj) {
        if (!obj.parent) {
          //
          return obj;
        }
        for (var i = 0; i < obj.parent.childNodes.length; i++) {
          var item = obj.parent.childNodes[i];
          if (item.label == obj.label) {
            tier.push(i);
            break;
          }
        }
        return findParent(obj.parent);
      };
      findParent(node); //
      var tempObj = this.data; //children
      for (var i = tier.length; i > 0; i--) {
        tempObj = tempObj[tier[i - 1]].children;
      }
      resolve(tempObj); // 

      //                resolve(this.readchildNode(tempObj))// 
    },
    /*
            removeCurrent(arr = []) {
                arr = Array.from(arr)
                if (arr.indexOf('current') !== -1) {
                    arr.splice(arr.indexOf('current'), 1)
                }
                return arr.join(' ')
            }
            */
    renderContent(h, { node, data, store }) {
      if (this.rend) {
        return this.rend(h, { node, data, store });
      }
      return (
        <span>
          <span>{node.label}</span>
        </span>
      );
    }
  },
  mounted: function() {
    let me = this,
      timer;
    //
    // me.$nextTick(function() {
    //   timer = setTimeout(() => {
    //     let treeNodes = me.$el.querySelectorAll(".el-tree-node"),
    //       firstNode,
    //       className,
    //       index = 0;
    //     if (treeNodes.length > 0) {
    //       firstNode = treeNodes[0];
    //       className = firstNode.className;
    //       firstNode.className = className + " " + "is-current";
    //       for (var i = 0; i < treeNodes.length; i++) {
    //         //                            treeNodes[i].className = me.removeCurrent(treeNodes[i].classList)
    //         //
    //         treeNodes[i].onclick = function(e) {
    //           //                                this.className = this.className + ' current'
    //           if (!index) {
    //             firstNode.className = className;
    //           }
    //           index++;
    //           if (timer) clearTimeout(timer);
    //         };
    //       }
    //     }
    //   }, 500);
    // });
    this.setHeight();
  }
};
</script>

<style scoped>
.base-tree {
  overflow: auto;
  /* border: 1px solid #d1dbe5; */
}

.base-tree .el-tree {
  border: none;
}

/*.base-tree .el-tree .el-tree-node.current > .el-tree-node__content {*/
/*background-color: #c1e5fd;*/
/*}*/
</style>



