const app = {
    state: {
        userInfo: JSON.parse(localStorage.getItem("userInfo") || "{}"),
        tabs: JSON.parse(localStorage.getItem("tabs") || "[]"),
        isloading: 0, //0 1loading,2main-loading
        platRoute: []
    },
    mutations: {
        setUserInfo(state, payload) {
            state.userInfo = payload;
            localStorage.setItem("userInfo", JSON.stringify(state.userInfo)); // tabs
        },
        addTab(state, payload) {
            // 
            if (!state.tabs.some(item => {
                    return item.path === payload.path;
                })) {
                state.tabs.push({
                    name: payload.meta.name || payload.name,
                    path: payload.path,
                    type: payload.meta.type,
                    id: payload.meta.id.toString() || payload.path,
                    noClose: payload.meta.noClose
                });
                localStorage.setItem("tabs", JSON.stringify(state.tabs)); // tabs
            }
        },
        removeTab(state, payload) {
            for (let i = 0, len = state.tabs.length; i < len; i++) {
                if (state.tabs[i].path === payload.path) {
                    state.tabs.splice(i, 1);
                    localStorage.setItem("tabs", JSON.stringify(state.tabs)); // tabs
                    break;
                }
            }
        },
        clearTabs(state) {
            state.tabs = [];
            localStorage.setItem("tabs", []);
        },
        setPlat(state, payload) {
            state.platRoute = payload;
        },
        setLoading(state, payload) {
            state.isloading = payload;
            setTimeout(() => {
                if (state.isloading) {
                    state.isloading = false;
                }
            }, 20000)
        }
    },
    actions: {}

}
export default app


