<template>
  <div class="base-selectTree" :style="style" clearable>
    <i :class="['base-selectTree__icon','el-icon-caret-top',{'is-reverse':showTree}]" @click="clickInput"></i>
    <i class="base-selectTree__icon el-icon-error" v-show="text" @click="clickClear"></i>
    <div class="select-wrap">
      <!-- <span v-if="multiple">
                <span v-for="item in multipleData" :key="item[nodeKey]">{{item[props.label]}}</span>
            </span> -->
      <el-input :disabled="disabled" @click="clickInput" readonly placeholder="..." v-model="text"></el-input>
    </div>
    <!---->
    <el-tree v-if="!load" :ref="id" :node-key="nodeKey" :current-node-key="currentNodeKey" class="base-selectTree-tree" :indent="10" :filter-node-method="filterNode" v-show="showTree" :data="data" :props="props" highlight-current :default-expand-all="defaultExpandAll" @node-click="handleNodeClick" :expand-on-click-node="false" :style="styleAttr">
    </el-tree>
    <!---->
    <el-tree v-else :ref="id" :node-key="nodeKey" :current-node-key="currentNodeKey" class="base-selectTree-tree" highlight-current :indent="10" :filter-node-method="filterNode" v-show="showTree" :data="data" :props="props" lazy @node-click="handleNodeClick" :load="loadNode" :expand-on-click-node="false" :style="styleAttr">
    </el-tree>
  </div>
</template>
<script>
import { getListByTree } from "@/util/util.js";
export default {
  props: {
    value: {
      type: [Number, String, Array, Object]
    },
    id: {
      type: String,
      default: "baseDivSelectTree"
    },
    //
    nodeKey: {
      type: String,
      default: "id"
    },
    //
    parentKey: {
      type: String,
      default: "parentId"
    },
    //
    multiple: {
      default: false
    },
    //
    defaultExpandAll: {
      type: Boolean,
      default: true
    },
    // id
    hideNode: {
      type: [Number, String]
    },
    //
    load: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    textP: {
      default: ""
    },
    width: {
      default: "200px"
    },
    //
    data: {
      type: Array,
      default: []
    },
    props: {
      type: Object,
      default() {
        return {
          children: "children",
          label: "name"
        };
      }
    },
    realVal: String, //
    styleAttr: {
      type: String
    },

    loadNode: {
      type: Function,
      default(node, resolve) {
        //
        let tier = []; // ()
        if (node.level == 0) {
          resolve(this.data); //
          return;
        }
        var findParent = function(obj) {
          if (!obj.parent) {
            //
            return obj;
          }
          for (var i = 0; i < obj.parent.childNodes.length; i++) {
            var item = obj.parent.childNodes[i];
            if (item.label == obj.label) {
              tier.push(i);
              break;
            }
          }
          return findParent(obj.parent);
        };
        findParent(node); //
        var tempObj = this.data; //children
        for (var i = tier.length; i > 0; i--) {
          tempObj = tempObj[tier[i - 1]].children
            ? tempObj[tier[i - 1]].children
            : [];
        }
        resolve(tempObj); // 

        //                resolve(this.readchildNode(tempObj))// 
      }
    }
  },
  data() {
    return {
      text: "",
      showTree: false,
      style: {
        // width: this.width
        //                    margin: "0 auto",
      },
      currentNodeKey: "", //
      realText: "", //text
      index: 0
    };
  },
  computed: {
    multipleData() {
      let textArr = this.text ? this.text.split(";") : [],
        me = this,
        multipleData = [];
      textArr.forEach(item => {
        me.findNode(me.data, item, multipleData);
      });
      return multipleData;
    },
    // list
    treeToList() {
      return getListByTree(this.data, this.nodeKey, "children", this.parentKey);
    }
  },
  methods: {
    clickInput() {
      if (this.disabled) {
        return false;
      }
      this.$refs[this.id].filter("");
      this.showTree = !this.showTree;
      if (this.showTree) {
        setTimeout(() => {
          this.setScrollTop();
        }, 100);
      }
      if (!this.index) {
        let width = this.$el.clientWidth;
        this.$el.querySelector(".base-selectTree-tree").style.width =
          width + "px";
      }
      this.index++;
    },
    handleNodeClick(data) {
      if (this.multiple) {
        //
        //                    if (!this.text || (this.text && this.text.indexOf(data[this.props.label]) == -1)) {
        if (this.text === undefined) {
          this.text = "";
        }
        if (this.text.indexOf(data[this.props.label]) == -1) {
          this.text = this.text
            ? this.text + ";" + data[this.props.label]
            : this.text + data[this.props.label];
        } else {
          let index = this.text.indexOf(data[this.props.label]),
            length = data[this.props.label].length;
          if (index === 0) {
            this.text =
              this.text.slice(0, index) + this.text.slice(index + length + 1);
          } else {
            this.text =
              this.text.slice(0, index - 1) + this.text.slice(index + length);
          }
        }
        this.$emit(
          "input",
          this.multipleData
            .map(item => {
              return item[this.nodeKey];
            })
            .join(",")
        );
        this.$emit("change", this.multipleData);
      } else {
        //
        let lastSelect = {};
        if (this.value) {
          lastSelect = this.findNodeByValue(this.value);
        }
        this.text = this.realVal ? data[this.realVal] : data[this.props.label]; //realvalprops
        this.showTree = false;
        this.$emit("input", data[this.nodeKey]);
        this.$emit("change", data, lastSelect);
      }
    },
    readchildNode(arr, callback) {
      //
      let me = this;
      var tempData = [];
      if (arr) {
        arr.forEach(function(item) {
          let obj = {};
          obj[me.props["label"]] = item[me.props["label"]];
          obj.id = item.id;
          tempData.push(obj);
        });
      }
      return tempData;
    },
    clickClear() {
      this.text = "";
      this.$emit("input", "");
    },
    setText(val, realVale) {
      //
      this.text = val;
      if (realVale) {
        this.currentNodeKey = this.getCurrentNodeKey(realVale);
      } else {
        this.currentNodeKey = this.getCurrentNodeKey(val);
      }
    },
    closeTree() {
      this.showTree = false;
    },
    findNode(arr = [], value, multipleData = []) {
      let me = this;
      arr.forEach(val => {
        if (val[me.props.label] == value) {
          multipleData.push(val);
        }
        if (val.children && val.children.length > 0) {
          me.findNode(val.children, value, multipleData);
        }
      });
    },
    findNodeByValue(value) {
      let node = null;
      this.$each(this.treeToList, item => {
        if (item[this.nodeKey] === value) {
          node = item;
        }
      });
      return node;
    },
    //
    getCurrentNodeKey(val) {
      let list = this.treeToList;
      for (let i = 0, len = list.length; i < len; i++) {
        if (list[i][this.props.label] == val) {
          return list[i][this.nodeKey];
        }
      }
    },
    setScrollTop() {
      let selectTree = this.$el.querySelector(".base-selectTree-tree"),
        curentKey = this.$el.querySelector(".is-current");
      selectTree.scrollTop = curentKey ? curentKey.offsetTop - 50 : 0;
    },
    filterNode(value, data) {
      // id
      if (this.hideNode) {
        let node = this.$refs[this.id].getNode(data);
        while (node.key) {
          if (node.data[this.nodeKey] === this.hideNode) {
            return false;
          }
          node = node.parent;
        }
      }

      if (!value) {
        return true;
      }
      var splitValue = value.split(";");
      if (
        this.multiple &&
        this.treeToList.some(item => {
          return item[this.props.label] === splitValue[splitValue.length - 1];
        })
      ) {
        return true;
      }
      return data[this.props.label].indexOf(value) !== -1;
    }
  },
  watch: {
    textP(val) {
      this.text = val;
    },
    text(val) {
      if (this.text === "") {
        this.$emit("textNull");
      }
      this.$refs[this.id].filter(val);
    },
    value(val) {
      if (val) {
        let curNode = this.findNodeByValue(val);
        if (curNode && curNode[this.props.label] !== this.text) {
          this.text = curNode[this.props.label];
        }
      } else {
        this.text = "";
      }
    }
  },
  mounted() {
    let textData = "";
    if (this.value === undefined || this.value === null) {
    } else {
      let curNode = this.findNodeByValue(this.value);
      if (curNode && curNode[this.props.label] !== this.text) {
        textData = curNode[this.props.label];
      } else {
        textData = this.value;
      }
    }
    this.text = textData;

    let me = this;
    document.addEventListener("click", function(e) {
      let flag = false;
      for (var i = 0; i < me.$el.childNodes.length; i++) {
        var item = me.$el.childNodes[i];
        if (item == e.target) {
          flag = true;
          return;
        }
      }
      me.showTree = flag;
    });
  }
};
</script>

<style>
.base-selectTree {
  position: relative;
}
.base-selectTree .el-tree-node__expand-icon {
  /*border: 8px solid transparent;*/
  /*border-left-width: 9px;*/
  /*border-left-color: #97a8be;*/
}

.base-selectTree .el-tree-node__expand-icon.is-leaf {
  border-color: transparent;
}

.base-selectTree > input {
  width: 100%;
}

.base-selectTree > input[disabled] {
  background-color: #eef1f6;
  border-color: #d1dbe5;
  color: #bbb;
  cursor: not-allowed;
}

.base-selectTree-tree {
  min-height: 100px;
  max-height: 200px;
  overflow: auto;
  position: absolute;
  width: 100%;
  z-index: 4;
  /* position: fixed; */
  border: 1px solid #ddd;
}

.base-selectTree__icon {
  color: #bfcbd9;
  font-size: 12px;
  transition: transform 0.3s;
  transform: translateY(-50%) rotate(180deg);
  line-height: 16px;
  top: 50%;
  cursor: pointer;
  position: absolute;
  right: 8px;
  z-index: 2;
}

.base-selectTree__icon.el-icon-error {
  right: 25px;
}

.base-selectTree__icon.is-reverse {
  transform: translateY(-50%);
  /*top: 50%;*/
}
</style>



