<template>
  <div class="sys_echart" :id="id" :style="echartStyle" @click="selectedEchart" @resize="handleResize"></div>
</template>


<script>
import { randomUUID } from "@/util/util.js";
export default {
  props: {
    data: {
      type: Object
    },
    css: {
      type: Object,
      default() {
        return {
          width: "100%",
          height: "350px"
        };
      }
    }
  },
  computed: {
    echartStyle: {
      get() {
        var html = "";
        for (let key in this.css) {
          html += key + ":" + this.css[key] + ";";
        }
        return html;
      }
    }
  },
  data() {
    return {
      id: "echart_" + randomUUID(),
      myChart: {}
    };
  },
  mounted() {
    this.myChart = this.$echarts.init(document.getElementById(this.id));
    var option = {
      title: {
        text: ""
      },
      tooltip: {},
      legend: {
        data: [""]
      },
      xAxis: {
        data: ["", "", "", "", "", ""]
      },
      yAxis: {},
      series: [
        {
          name: "",
          type: "bar",
          data: [5, 20, 36, 10, 10, 20]
        }
      ]
    };

    // 
    this.myChart.setOption(option);
  },
  methods: {
    selectedEchart() {
      console.log(this.id);
    },
    handleResize() {
      this.myChart.resize();
    }
  }
};
</script>



