<template>
  <div class="radio-wrap">
    <div class="radio-block" v-for="(item, index) in dataLists" :key="index" :style="{width: width}">
      <el-button class="radio-btn" type="text" :class="{'active':value === item[valueKey],disabled:item.disabled}" :disabled="item.disabled" @click="clickHandle(item)">{{item[label]}}</el-button>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    value: {
      type: [String, Number, Boolean]
    },
    dataLists: {
      type: Array,
      default() {
        return [];
      }
    },
    width: {
      type: String,
      default: "50%"
    },
    label: {
      type: String,
      default: "name"
    },
    valueKey: {
      type: String,
      default: "id"
    }
  },
  data() {
    return {};
  },
  watch: {
    dataLists(lists) {
      this.$emit("input", lists[0][this.valueKey]);
    }
  },
  methods: {
    clickHandle(data) {
      if (this.value !== data[this.valueKey]) {
        this.$emit("input", data[this.valueKey]);
        this.$emit("change", data);
      }
    }
  },
  mounted() {
    if (!this.value && this.dataLists.length > 0) {
      this.$emit("input", this.dataLists[0][this.valueKey]);
    }
  }
};
</script>

<style scoped>
.radio-wrap {
  margin: -5px -5px 0 0;
  width: auto;
  display: inline-block;
}
.radio-block {
  display: inline-block;
  padding: 5px 5px 0 0;
}
.radio-btn {
  background-color: dodgerblue;
  color: #fff;
  padding: 10px 15px;
  height: auto;
  border-radius: 0;
  margin: 0;
  width: 100%;
}
.radio-btn.active {
  background-color: deepskyblue;
}
.radio-btn.disabled {
  background-color: #ddd;
}
</style>



