import axios from 'axios'
import Vue from './vueutil'
import vm from "../main.js";
import {
    getCookie,
    delCookie,
    varType
} from "./util";
import {
    Message,
    MessageBox
} from 'element-ui';

import router from '@/router'

//axios
axios.defaults.timeout = 50000;
axios.defaults.withCredentials = true;
axios.defaults.baseURL = process.env.API_HOST; //url;
axios.defaults.headers['Content-Type'] = 'application/json;charset=UTF-8';

var loginOut = function() {
    window.sessionStorage.clear();
    delCookie('name');
    delCookie('st');
    window.location.href = Vue.prototype.homePage;
}

// let loadingTime;
let loading;
//axios request 
axios.interceptors.request.use(
    config => {
        const token = getCookie('st'); //Cookie
        //todo 
        // if (!token) {
        //     loginOut();
        // }
        //noLoading,loading
        if (!config.noLoading) {
            loading = config.loadingTarget;
            if (config.loadingTarget && !(varType(config.loadingTarget) === "number")) {
                // loadingTime = new Date().getTime();
                config.loadingTarget.openLoading();
            } else {
                try {
                    vm.$store.commit("setLoading", config.loadingTarget || 2)
                } catch (error) {
                    console.log(error);
                }
            }
        }
        // if (typeof config.resoureType === "undefined") {
        //     config.headers["mm"] = vm.$route.query.id;
        // } else {
        //     config.headers["mm"] = config.resoureType;
        // }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);

//axios response  
axios.interceptors.response.use(
    response => {
        var config = response.config;
        if (!config.noLoading) {
            let fn = config.loadingTarget ? function() {
                config.loadingTarget.closeLoading();
            } : function() {
                vm.$store.commit("setLoading", 0);
            }
            setTimeout(() => {
                fn();
            }, 500);
        }
        return response;
    },
    error => {
        setTimeout(() => {
            if (loading) {
                loading.closeLoading();
            } else {
                vm.$store.commit("setLoading", 0);
            }
        }, 500);
        switch (error.response.status) {
            case 40201:
                //todo 
                loginOut();
                break;
            case 40301:
                Message({
                    showClose: true,
                    message: '',
                    type: 'error',
                    duration: 5 * 1000
                })
                break;
            case 40302:
                MessageBox.alert(',!', '', {
                    type: 'warning',
                    confirmButtonText: ''
                })
                break;
            case 40303:
                MessageBox.confirm(',', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    //todo 
                    loginOut();
                })
                break;
            case 40304:
                //TODO 
                window.location.href = 'err.html'
                break;
            case 502:
                loginOut();
                break;
        }
        return Promise.reject(error.response.data)
    });

export default axios;


