<template>
  <div class="inblock">
    <el-radio-group v-show="visibleType" class="ts-radio" v-model="type" @change="changeHandle">
      <el-radio-button v-for="(item, index) in showTime" :key="index" :label="item">{{riadoList[item.toString()]?riadoList[item.toString()].label:""}}</el-radio-button>
    </el-radio-group>
    <da-time v-model="timeSelected" :label="label" :type="riadoList[type.toString()]?riadoList[type.toString()].timeName:''" :is-picker-end="isPickerEnd" :width="width" @change="changeInput"></da-time>
  </div>
</template>
<script>
import { cycleTime } from "../../util/util";
export default {
  props: {
    value: {
      type: Array,
      default() {
        return [new Date(), new Date()];
      }
    },
    timeType: {
      type: [String, Number]
    },
    label: {
      type: Object,
      default() {
        return { sLabel: "", eLabel: "" };
      }
    },
    isPickerEnd: {
      type: Boolean,
      default: true
    },
    width: {
      type: String,
      default: "350px"
    },
    showTime: {
      type: Array,
      default() {
        return [3, 5, 8];
      }
    },
    // 
    visibleType: {
      type: Boolean,
      default: true
    }
  },
  data() {
    let time = [];
    let type = this.timeType;
    let riadoList = {
      3: {
        label: "",
        value: "day",
        timeName: "date",
        format: "YYYY-MM-DD"
      },
      5: {
        label: "",
        value: "month",
        timeName: "month",
        format: "YYYY-MM-01"
      },
      8: {
        label: "",
        value: "year",
        timeName: "year",
        format: "YYYY-01-01"
      }
    };
    if (!this.timeType) {
      this.$emit("update:timeType", this.showTime[0]);
      type = this.showTime[0];
    }
    if (!(this.value && this.value[0] && this.value[1])) {
      let date = this.$moment(new Date(), riadoList[type.toString()].format);
      this.$emit("input", [date, date]);
      time = [date, date];
    } else {
      time = this.value;
    }
    return {
      timeSelected: time,
      riadoList: riadoList,
      cycle: this.$store.state.targetConstant.targetCycle,
      type: type
    };
  },
  watch: {
    value: {
      handler() {
        if (this.timeSelected !== this.value) {
          this.timeSelected = this.value;
        }
      },
      deep: true
    },
    timeType() {
      if (this.timeType && this.timeType !== this.type) {
        this.type = this.timeType;
      }
    }
  },
  methods: {
    changeHandle() {
      this.$emit("update:timeType", this.type);
      this.setData(this.timeSelected);
      this.$emit("radioChange", this.value, this.type);
    },
    changeInput(value) {
      this.setData(value);
      this.$emit("inputChange", value, this.type);
    },
    setData(value) {
      let data = [
        value[0]
          ? this.$moment(value[0], this.riadoList[this.type.toString()].format)
          : "",
        value[1] ? cycleTime(value[1], this.type)[1] : ""
      ];
      this.$emit("input", data);
      this.$emit("change", data, this.type);
    }
  },
  mounted() {}
};
</script>

<style scoped>
.ts-radio {
  vertical-align: top;
}
.el-input {
  width: 120px;
}
</style>



