<template>
  <div :class="className" :id="id" :style="{height:height,width:width}">
  </div>
</template>

<script>
import echarts from "echarts";
import resize from "./mixins/resize";
import { dateCalcRound } from "@/util";
import { varType, deepJSONCopy, formatTime, extend } from "../../util/util";

export default {
  mixins: [resize],
  props: {
    className: {
      type: String,
      default: "chart"
    },
    legendDisplace: {
      type: Object,
      default() {
        return {};
      }
    },
    id: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "250px"
    },
    title: {
      type: String
    },
    lineData: {
      type: Object
    },
    // legend
    label: {
      type: String,
      default: "name"
    },
    unit: {
      type: String,
      default: "targetUnit"
    },
    // 
    valueKey: {
      type: String,
      default: "dataValue"
    },
    // x
    xAxisKey: {
      type: String,
      default: "dataTime"
    },
    // y
    nyAxis: {
      type: [Number, Array],
      default: 1
    },
    // 
    isStack: {
      type: Boolean,
      defautl: false
    },
    // 
    hasDataZoom: {
      type: Boolean,
      default: false
    },
    isStep: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: "line" // line bar
    },
    // x
    xAxisFormat: {
      type: [String] // 
    },
    // x
    format: {
      type: String
    },
    // label
    legendFormat: {
      type: String
    },
    // 
    dataLists: {
      type: Array,
      default() {
        return [];
      }
    },
    dateTime: {
      // [dataxAxisKey][20172017-012017-01-01][x]
      type: [String, Array]
    },
    // otherAttr: {
    //   type: Object,
    //   default() {
    //     return {
    //       unit: "targetUnit"
    //     };
    //   }
    // },
    // 20180904 
    lineDataAttr: {
      type: Array,
      default() {
        return ["target", "benchmark", "quota"];
      }
    },

    // echart
    cusProps: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      chart: null,
      xAxis: [], // 
      truexAxis: [], // 
      options: {} // 
    };
  },
  watch: {
    dataLists: {
      handler(lists) {
        if (this.dataLists.length === 0) {
          document.getElementById(this.id).innerHTML = this.chartNoData();
          return;
        }
        this.truexAxis = [];
        this.calcTime(this.dateTime);
        let legend = [];
        let series = [];
        let xAxis = [];
        let yAxis = [];
        // x
        if (this.dateTime) {
          this.$each(lists, item => {
            let trueName = this.legendDisplace[item[this.label]] // 
              ? this.legendDisplace[item[this.label]]
              : item[this.label] || this.label;
            if (this.legendFormat) {
              trueName = this.$moment(trueName, this.legendFormat);
            }
            let serieIndex = legend.indexOf(trueName); // 
            let dataIndex = -1; // x
            // x
            if (this.xAxisFormat) {
              dataIndex = this.xAxis.indexOf(
                this.$moment(new Date(item[this.xAxisKey]), this.xAxisFormat)
              );
            } else {
              dataIndex = this.xAxis.indexOf(item[this.xAxisKey]);
            }
            if (serieIndex === -1) {
              let yAxisIndex = yAxis.length - 1; // 
              if (varType(this.nyAxis) === "array") {
                let nyIndex = series.length;
                if (
                  nyIndex <= Math.max.apply(null, this.nyAxis) &&
                  !yAxis[nyIndex]
                ) {
                  let i = yAxis.length;
                  yAxis.push({
                    type: "value", // 
                    name: "(" + (item[this.unit] || this.unit || "-") + ")", // y
                    axisTick: {
                      show: false
                    },
                    position: i === 0 ? "left" : "right",
                    offset: i === 0 ? "" : 50 * (i - 1), // 
                    // splitNumber: 10, // 
                    splitLine: {
                      show: i === 0
                    },
                    axisLabel: {
                      margin: 10,
                      textStyle: {
                        fontSize: 14
                      }
                    }
                  });
                  yAxisIndex = this.nyAxis[nyIndex];
                }
              } else {
                if (yAxis.length < this.nyAxis) {
                  let i = yAxis.length;
                  yAxis.push({
                    type: "value", // 
                    name: "(" + (item[this.unit] || this.unit || "-") + ")", // y
                    axisTick: {
                      show: false
                    },
                    position: i === 0 ? "left" : "right",
                    offset: i === 0 ? "" : 50 * (i - 1), // 
                    // splitNumber: 10, // 
                    splitLine: {
                      show: i === 0
                    },
                    axisLabel: {
                      margin: 10,
                      textStyle: {
                        fontSize: 14
                      }
                    }
                  });
                  yAxisIndex = i;
                }
              }
              legend.push(trueName);
              series.push({
                stack: this.isStack ? "one" : null,
                yAxisIndex: yAxisIndex,
                smooth: !this.isStep,
                step: this.isStep,
                name: trueName,
                type: this.type,
                data: Array(this.xAxis.length).fill("-")
              });
              serieIndex = legend.length - 1;
            }
            if (dataIndex === -1) {
              console.warn(item[this.xAxisKey] + "x", this.xAxis);
            } else {
              series[serieIndex].data[dataIndex] = item[this.valueKey];
            }
          });
          xAxis = this.xAxis;
        } else {
          let newLists = this.sortData(deepJSONCopy(lists));
          this.$each(newLists, (item, index) => {
            let trueName = this.legendDisplace[item[this.label]] // 
              ? this.legendDisplace[item[this.label]]
              : item[this.label] || this.label;
            let serieIndex = legend.indexOf(trueName); // 

            // x
            let formatXAis = this.xAxisFormat
              ? this.$moment(new Date(item[this.xAxisKey]), this.xAxisFormat)
              : item[this.xAxisKey];
            let dataIndex = xAxis.indexOf(formatXAis); // x
            if (serieIndex === -1) {
              let yAxisIndex = yAxis.length - 1; // 
              // Y
              if (varType(this.nyAxis) === "array") {
                let nyIndex = series.length;
                if (
                  nyIndex <= Math.max.apply(null, this.nyAxis) &&
                  !yAxis[nyIndex]
                ) {
                  let i = yAxis.length;
                  yAxis.push({
                    type: "value", // 
                    name: "(" + (item[this.unit] || this.unit || "-") + ")", // y
                    axisTick: {
                      show: false
                    },
                    position: i === 0 ? "left" : "right",
                    offset: i === 0 ? "" : 50 * (i - 1), // 
                    // splitNumber: 10, // 
                    splitLine: {
                      show: i === 0
                    },
                    axisLabel: {
                      margin: 10,
                      textStyle: {
                        fontSize: 14
                      }
                    }
                  });
                  yAxisIndex = this.nyAxis[nyIndex];
                }
              } else {
                if (yAxis.length < this.nyAxis) {
                  let i = yAxis.length;
                  yAxis.push({
                    type: "value",
                    name: "(" + (item[this.unit] || this.unit || "-") + ")",
                    axisTick: {
                      show: false
                    },
                    position: i === 0 ? "left" : "right",
                    offset: i === 0 ? "" : 50 * (i - 1), // 
                    // splitNumber: 10, // 
                    splitLine: {
                      show: i === 0
                    },
                    axisLabel: {
                      margin: 10,
                      textStyle: {
                        fontSize: 14
                      }
                    }
                  });
                  yAxisIndex = i;
                }
              }
              legend.push(trueName);
              series.push({
                stack: this.isStack ? "one" : null,
                yAxisIndex: yAxisIndex,
                name: trueName,
                type: this.type,
                smooth: !this.isStep,
                step: this.isStep,
                data: Array(xAxis.length).fill("-")
              });
              serieIndex = legend.length - 1;
            }
            // 
            if (dataIndex === -1) {
              xAxis.push(formatXAis);
              this.truexAxis.push(item[this.xAxisKey]);
              this.$each(series, item => {
                item.data.push("-");
              });
              dataIndex = xAxis.length - 1;
            }
            series[serieIndex].data[dataIndex] = item[this.valueKey];
          });
          this.xAxis = xAxis;
        }
        this.initChart(xAxis, legend, yAxis, series);
      },
      deep: true
    }
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  // computed: {
  //   unit() {
  //     return this.dataLists[0][this.unit];
  //   }
  // },
  methods: {
    // 
    setLineData() {
      let series = [];
      let legend = [];
      let xAxis = this.truexAxis;

      // 
      let lineDataAttr = {
        target: {
          name: "",
          yIndex: 0,
          attr: "target",
          color: "#2eff2e"
        },
        benchmark: {
          name: "",
          yIndex: 0,
          attr: "benchmark",
          color: "#2eff2e"
        },
        quota: {
          name: "",
          yIndex: 0,
          attr: "quota",
          color: "#c23531"
        }
      };

      this.lineDataAttr.forEach(item => {
        let seriesInitObj = {};
        let attr = "";
        let j = 0;
        if (varType(item) === "string") {
          attr = item;
          seriesInitObj = lineDataAttr[item];
        } else if (varType(item) === "object") {
          attr = item.attr;
          seriesInitObj = extend({}, lineDataAttr[item.attr], item);
        } else {
          console.warn("");
          return false;
        }
        let orginData = this.lineData[attr];
        let dealData = new Array(xAxis.length).fill("-");
        if (orginData && orginData.length > 0) {
          for (let i = 0, len = orginData.length; i < len; i++) {
            let sTime = new Date(orginData[i].startTime).getTime();
            let eTime = new Date(orginData[i].endTime).getTime();
            for (; j < xAxis.length; j++) {
              let curTime = new Date(xAxis[j]).getTime();
              if (curTime >= sTime) {
                if (curTime <= eTime) {
                  dealData[j] = orginData[i].value;
                } else {
                  break;
                }
              }
            }
          }
          series.push({
            id: attr,
            name: seriesInitObj.name,
            type: this.type,
            itemStyle: {
              normal: {
                color: seriesInitObj.color
              }
            },
            yAxisIndex: seriesInitObj.yIndex,
            data: dealData
          });
          legend.push(seriesInitObj.name);
        }
      });

      return {
        series,
        legend
      };
    },
    sortData(lists) {
      return lists.sort((a, b) => {
        let aToTime = new Date(a[this.xAxisKey]);
        let bToTime = new Date(b[this.xAxisKey]);
        if (aToTime > bToTime) {
          return 1;
        } else if (aToTime < bToTime) {
          return -1;
        } else {
          return 0;
        }
      });
    },
    beforeDestroy() {
      if (!this.chart) {
        return;
      }
      this.chart.dispose();
      this.chart = null;
    },
    xAxisFormatter(value, index) {
      if (isNaN(Number(value))) {
        return value;
      }
      let date = new Date(Number(value));
      if (isNaN(date.getTime())) {
        date = value;
      } else {
        date = this.$moment(date, "YYYY-MM-DD");
      }
      return date;
    },
    calcTime(value) {
      if (this.dateTime && varType(this.dateTime) === "string") {
        let timeFormat = value;
        if (this.format) {
          timeFormat = value
            .split(",")
            .map(item => {
              return this.$moment(new Date(item), this.format);
            })
            .join(",");
        }
        if (this.xAxisFormat) {
          this.xAxis = dateCalcRound(timeFormat).map(item => {
            this.truexAxis.push(item);
            return this.$moment(new Date(item), this.xAxisFormat);
          });
        } else {
          this.xAxis = dateCalcRound(timeFormat);
          this.truexAxis = [].concat(timeFormat);
        }
      } else if (varType(this.dateTime) === "array") {
        this.xAxis = this.dateTime;
      }
    },
    changeTarget(data) {
      this.lineData = data;
      let benchmark = this.setLineData();
      let index = -1;
      let seriesData = this.options.series;
      let legend = this.options.legend;
      this.$each(benchmark.series, (item, j) => {
        let index = -1;
        this.$each(seriesData, (serie, i) => {
          if (serie.id === item.id) {
            index = i;
            seriesData[i] = item;
            legend[i] = benchmark.legend[j];
          }
        });
        if (index === -1) {
          seriesData.push(item);
          legend.push(benchmark.legend[j]);
        }
      });
      this.chart.setOption(this.options, true);
    },

    initChart(xAxis, legend, yAxis, series) {
      if (this.chart) {
        this.beforeDestroy();
      }

      //  
      if (this.lineData) {
        let benchmark = this.setLineData();
        series = series.concat(benchmark.series);
        legend = legend.concat(benchmark.legend);
      }

      this.chart = echarts.init(document.getElementById(this.id), "shine");
      let me = this;
      let obj = {
        title: {
          top: 20,
          text: this.title,
          textStyle: {
            fontWeight: "normal",
            fontSize: 16
          },
          left: "30%"
        },
        tooltip: {
          trigger: "axis",
          // formatter: (params, ticket, callback) => {
          //   console.log(params);

          //   return "111";
          // },
          axisPointer: {
            type: "cross",
            animation: false,
            label: {
              backgroundColor: "#505765"
            }
          }
        },
        legend: {
          top: 20,
          icon: "rect",
          itemWidth: 14,
          itemHeight: 5,
          itemGap: 13,
          data: legend,
          right: "4%",
          textStyle: {
            fontSize: 12
            // color: "#F1F1F3"
          }
        },
        grid: {
          // top: 100,
          left: "3%",
          right: "4%",
          bottom: "2%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: this.type === "line" ? false : true,
            axisLine: {
              lineStyle: {
                // color: "#57617B"
              }
            },
            axisLabel: {
              // formatter(value, index) {
              //   return me.xAxisFormatter(value, index);
              // }
            },
            data: xAxis
          }
        ],
        yAxis: yAxis,
        series: series
      };
      // 
      if (this.hasDataZoom) {
        obj.dataZoom = [
          {
            show: true,
            realtime: true,
            start: 65,
            end: 85
          },
          {
            type: "inside",
            realtime: true,
            start: 65,
            end: 85
          }
        ];
        obj.grid = {
          bottom: 60
        };
      }

      // echart
      extend(obj, this.cusProps);
      this.options = obj;
      this.chart.setOption(obj, true);
    }
  },
  mounted() {
    // if (this.dateTime) {
    //   this.calcTime(this.dateTime);
    // }
  }
};
</script>



