import {
    varType,
    extend,
    trimAll,
    changeArr
} from './util';
import Vue from "./vueutil";

/**
 * (elementui)
 * <el-form :rule="ruleForm"></el-form>
 *
 * data() {
 *   ruleForm:this.eachValidate({
 *      name:{required:true},
 *      email:{email:true, required:true}
 *   })
 * }
 *
 * 
 * required:true()
 * tel:true()
 * len:5(5)  min:5(5)  max:10(10) range:[5,10](510)
 * number:true()
 * digits:true()
 * url:true
 * email:true
 * creditcard:true()
 * accept : ".jpg|.png"()
 * idCardNo : true()
 * mobile : true()
 * tel : true()
 * telAndMobile : true(/)
 * code : true()
 *
 * stringCheck : true,
 * account: true()
 * alnum : true()
 * chcharacter : true()
 * imeDisabled: true()
 * string : true()
 * letter  true()
 *
 * notSpace : true()
 * exclusive:"abc"()
 * unequal:"abc"()
 * dateTime: true(,yyyy-mm-dd)
 *
 * lenString : 5(5)
 * rangeString : [5,10](510)
 * minString : 10()
 * maxString : 50()
 *
 * min:5(5)
 * max:10(10)
 * range:[5,10](510)
 * len: 5(5)
 *
 * remote:""/[]/{}(ajax)urlget[url, paramsName]getajax{}
 * customVail:funcitnName()
 */

// 15
function isDate6(sDate) {
    if (!/^[0-9]{6}$/.test(sDate)) {
        return false;
    }
    var year, month, day;
    year = sDate.substring(0, 4);
    month = sDate.substring(4, 6);
    if (year < 1800 || year > 2100)
        return false
    if (month < 1 || month > 12)
        return false
    return true
}

// 18
function isDate8(sDate) {
    if (!/^[0-9]{8}$/.test(sDate)) {
        return false;
    }
    var year, month, day;
    year = sDate.substring(0, 4);
    month = sDate.substring(4, 6);
    day = sDate.substring(6, 8);
    var iaMonthDays = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
    if (year < 1800 || year > 2100)
        return false
    if (((year % 4 == 0) && (year % 100 != 0)) || (year % 400 == 0))
        iaMonthDays[1] = 29;
    if (month < 1 || month > 12)
        return false
    if (day < 1 || day > iaMonthDays[month - 1])
        return false
    return true
}

var validate = {
    number: (rule, value, callback, source, options) => {
        if (value === '' || value === null) {
            return callback();

        }
        if (!isNaN(value)) { //varType(value) === "number"
            switch (rule.type) {
                case "number":
                    callback();
                    break;
                case "len":
                    if (value !== Number(rule.data)) {
                        callback(new Error((rule.name || "") + "" + rule.data));
                    } else {
                        callback();
                    }
                    break;
                case "min":
                    if (value < Number(rule.data)) {
                        callback(new Error((rule.name || "") + "" + rule.data));
                    } else {
                        callback();
                    }
                    break;
                case "max":
                    if (value > Number(rule.data)) {
                        callback(new Error((rule.name || "") + "" + rule.data));
                    } else {
                        callback();
                    }
                    break;
                case "range":
                    if (value < Number(rule.data[0]) || value > Number(rule.data[1])) {
                        callback(new Error((rule.name || "") + "" + rule.data[0] + "" + rule.data[1] + ""));
                    } else {
                        callback();
                    }
                    break;
            }
        } else {
            callback(new Error(""));
        }
    },
    string: (rule, value, callback, source, options) => {
        if (value === "" || value === undefined || value === null) {
            callback();
            return false;
        }
        if (varType(value) === "string") {
            var len = value.length;
            switch (rule.type) {
                case "len":
                    if (len !== Number(rule.data)) {
                        callback(new Error((rule.name || "") + "" + rule.data));
                    } else {
                        callback();
                    }
                    break;
                case "min":
                    if (len < Number(rule.data)) {
                        callback(new Error((rule.name || "") + "" + rule.data));
                    } else {
                        callback();
                    }
                    break;
                case "max":
                    if (len > Number(rule.data)) {
                        callback(new Error((rule.name || "") + "" + rule.data));
                    } else {
                        callback();
                    }
                    break;
                case "range":
                    if (len < Number(rule.data[0]) || value > Number(rule.data[1])) {
                        callback(new Error((rule.name || "") + "" + rule.data[0] + "" + rule.data[1] + ""));
                    } else {
                        callback();
                    }
                    break;
                case "exclusive":
                    if (value.indexOf(rule.data) > -1) {
                        callback(new Error((rule.name || "") + "" + rule.data));
                    } else {
                        callback();
                    }
                    break;
                case "unequal":
                    if (value === rule.data) {
                        callback(new Error((rule.name || "") + "" + rule.data + ""));
                    } else {
                        callback();
                    }
                    break;
            }
        } else {
            callback(new Error(""));
        }
    },
    creditcard: (rule, value, callback, source, options) => {
        value = trimAll(value); //
        if (/[^0-9 \-]+/.test(value)) {
            callback(new Error(rule.message));
        }
        var nCheck = 0,
            nDigit = 0,
            bEven = false;
        value = value.replace(/\D/g, "");
        for (var n = value.length - 1; n >= 0; n--) {
            var cDigit = value.charAt(n);
            nDigit = parseInt(cDigit, 10);
            if (bEven) {
                if ((nDigit *= 2) > 9) {
                    nDigit -= 9
                }
            }
            nCheck += nDigit;
            bEven = !bEven
        }
        if (!((nCheck % 10) === 0)) {
            callback(new Error(rule.message));
        } else {
            callback();
        }
    },
    idCardNo: (rule, value, callback, source, options) => {
        var factorArr = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2, 1); // 
        var parityBit = new Array("1", "0", "X", "9", "8", "7", "6", "5", "4", "3", "2"); // 
        var varArray = new Array(),
            i;
        var intValue;
        var lngProduct = 0;
        var intCheckDigit;
        var intStrLen = value.length;
        var idNumber = value;
        // 1518
        if ((intStrLen != 15) && (intStrLen != 18)) {
            callback(new Error(rule.message));
            return false;
        } else {
            for (i = 0; i < intStrLen; i++) {
                varArray[i] = idNumber.charAt(i);
                if ((varArray[i] < '0' || varArray[i] > '9') && (i != 17)) {
                    callback(new Error(rule.message));
                    return false;
                } else if (i < 17) {
                    varArray[i] = varArray[i] * factorArr[i];
                }
            }

            if (intStrLen == 18) { // 18
                // 
                var date8 = idNumber.substring(6, 14);
                if (isDate8(date8) == false) {
                    callback(new Error(rule.message));
                    return false;
                }
                // 17
                for (i = 0; i < 17; i++) {
                    lngProduct = lngProduct + varArray[i];
                }
                // 
                intCheckDigit = parityBit[lngProduct % 11];
                // 
                if (varArray[17] != intCheckDigit) {
                    callback(new Error(rule.message));
                    return false;
                }
            } else { // 15
                // 
                var date6 = idNumber.substring(6, 12);
                if (isDate6(date6) == false) {
                    callback(new Error(rule.message));
                    return false;
                }
            }
            callback();
        }
    },
    accept: (rule, value, callback, source, options) => {
        if (new RegExp(rule.data + "$").test(value)) {
            callback();
        } else {
            callback(new Error("" + rule.data + ""));
        }
    },
    regExp: (rule, value, callback, source, options) => {
        if (value === "") {
            callback();
        } else {
            if (new RegExp(rule.data).test(value)) {
                callback();
            } else {
                callback(new Error(rule.message));
            }
        }
    },
    regExpEcho: (rule, value, callback, source, options) => {
        if (value === "") {
            callback();
        } else {
            if (new RegExp(rule.data).test(value)) {
                callback(new Error(rule.message));
            } else {
                callback();
            }
        }
    },
    regExpArr: (rule, value, callback, source, options) => {
        if (varType(rule.data) === "object") {
            rule.data.length = Object.keys(rule.data).length; //XXX 
            rule.data = changeArr(rule.data);
        }
        value = trimAll(value);
        if (value === "") {
            callback();
        } else {
            if (rule.data.some(function(item) {
                    return new RegExp(item).test(value);
                })) {
                callback();
            } else {
                callback(new Error(rule.message));
            }
        }
    },
    remote: (rule, value, callback, source, options) => {
        var ajaxObj = {
            method: "GET",
            url: "",
            params: {}
        };
        if (varType(rule.data) === "array") {
            ajaxObj.url = rule.data[0];
            ajaxObj.params[rule.data[1]] = value;
            // } else if (varType(rule.data) === "object") {
            //     ajaxObj = rule.data;
        } else {
            ajaxObj.url = rule.data;
            ajaxObj.params.account = value;
        }

        ajaxObj.noLoading = true;

        Vue.prototype.$ajax(ajaxObj).then((res) => {
            if (!res.data.success) {
                return callback(new Error(res.data.msg || ""))
            } else {
                callback();
            }
        }).catch((err) => {
            console.log(err);
        });
    }
};
var ruleType = {
    required: {
        required: true,
        message: '',
        trigger: 'blur'
    },
    email: {
        type: 'email',
        message: '',
        trigger: 'blur'
    },
    url: {
        type: 'url',
        message: '',
        trigger: 'blur'
    },
    //
    number: {
        validator: validate.number,
        type: 'number',
        message: "",
        trigger: 'blur'
    },
    digits: {
        type: 'integer',
        message: "",
        trigger: 'blur'
    },
    creditcard: {
        validator: validate.creditcard,
        message: "",
        trigger: 'blur'
    },
    len: {
        validator: validate.number,
        type: "len",
        trigger: 'blur'
    },
    min: {
        validator: validate.number,
        type: "min",
        trigger: 'blur'
    },
    max: {
        validator: validate.number,
        type: "max",
        trigger: 'blur'
    },
    range: {
        validator: validate.number,
        type: "range",
        trigger: 'blur'
    },

    accept: {
        validator: validate.accept,
        trigger: 'blur'
    },
    stringCheck: { //[a-zA-Z][A-Za-z0-9_]
        validator: validate.regExp,
        data: "^[\\u4E00-\\u9FA5A-Za-z0-9_]+$",
        message: "",
        trigger: 'blur'
    },
    account: {
        validator: validate.regExp,
        data: "^[A-Za-z0-9_]+$",
        message: "",
        trigger: 'blur'
    },
    alnum: {
        validator: validate.regExp,
        data: "^[a-zA-Z0-9]+$",
        message: "",
        trigger: 'blur'
    },
    chcharacter: {
        validator: validate.regExp,
        data: "^[\\u4e00-\\u9fa5]+$",
        message: "",
        trigger: 'blur'
    },
    imeDisabled: {
        validator: validate.regExpEcho,
        data: "^[\\u4e00-\\u9fa5]+$",
        message: "",
        trigger: 'blur'
    },
    letter: {
        validator: validate.regExp,
        data: "^[a-zA-Z]+$",
        message: "",
        trigger: 'blur'
    },
    idCardNo: {
        validator: validate.idCardNo,
        message: "",
        trigger: 'blur'
    },
    mobile: {
        validator: validate.regExp,
        data: "^(\\d{3}|\\d{4})?(\\d{8}|\\d{7})?(\\d{1,6})?$",
        message: "",
        trigger: 'blur'
    },
    tel: {
        validator: validate.regExp,
        data: "^(0[0-9]{2,3}\\-)?([2-9][0-9]{6,7})+(\\-[0-9]{1,4})?$",
        message: "",
        trigger: 'blur'
    },
    telAndMobile: {
        validator: validate.regExpArr,
        data: ["^(0[0-9]{2,3}\\-)?([2-9][0-9]{6,7})+(\\-[0-9]{1,4})?$", "^(\\d{3}|\\d{4})?(\\d{8}|\\d{7})?(\\d{1,6})?$"],
        message: "",
        trigger: 'blur'
    },
    code: {
        validator: validate.regExp,
        data: "[1-9]\\d{5}(?!\\d)",
        message: "",
        trigger: 'blur'
    },
    notSpace: {
        validator: validate.regExp,
        data: "^[^\\s]+$",
        message: "",
        trigger: 'blur'
    },

    string: {
        type: 'string',
        message: "",
        trigger: 'blur'
    },
    lenString: {
        validator: validate.string,
        type: "len",
        trigger: 'blur'
    },
    unequal: {
        validator: validate.string,
        type: "unequal",
        trigger: 'blur'
    },
    exclusive: {
        validator: validate.string,
        type: "exclusive",
        trigger: 'blur'
    },
    minString: {
        validator: validate.string,
        type: "min",
        trigger: 'blur'
    },
    maxString: {
        validator: validate.string,
        type: "max",
        trigger: 'blur'
    },
    rangeString: {
        validator: validate.string,
        type: "range",
        trigger: 'blur'
    },
    dateTime: {
        validator: validate.regExp,
        data: "^(\\d{4})-(0\\d{1}|1[0-2])-(0\\d{1}|[12]\\d{1}|3[01])$",
        message: "",
        trigger: 'blur'
    },
    remote: {
        validator: validate.remote,
        trigger: 'blur'
    },
    customVail: {
        trigger: 'blur'
    }
};

export default ruleType;


