<template>
  <div class="date-time" :style="{width:width}">
    <el-date-picker v-if="type==='datetimerange' || type==='daterange'" v-model="time" :type="type" placeholder="" :range-separator="rangeSeparator" :start-placeholder="startPlaceholder" :editable="false"></el-date-picker>

    <div v-else class="date-time-wrap">
      <span v-if="label.sLabel">{{label.sLabel}}</span>
      <el-date-picker class="date-time-input" v-model="stime" :format="changeFormat(format[type])" :type="type" @change="handleStartTime" placeholder="" :editable="false">
      </el-date-picker>
      <span>{{label.eLabel}}</span>
      <el-date-picker class="date-time-input" v-model="etime" :format="changeFormat(format[type])" :type="type" @change="handleEndTime" :picker-options="pickerOptions" placeholder="" :editable="false">
      </el-date-picker>
    </div>
  </div>
</template>

<script>
/**
 * 
 *   
 *
 */
export default {
  props: {
    type: {
      type: String,
      default: "date" //year/month/date/week/datetime datetimerange/daterange
    },
    width: {
      type: String,
      default: "100%"
    },
    label: {
      type: Object,
      default() {
        return {
          eLabel: ""
        };
      }
    },
    rangeSeparator: {
      type: String
    },
    startPlaceholder: {
      type: String
    },
    isPickerEnd: {
      type: Boolean,
      default: true
    },
    value: {
      // value
      default: ""
    },
    pickerOptions: {
      type: Object,
      default() {
        return {
          disabledDate: time => {
            return (
              this.isPickerEnd &&
              time.getTime() <
                (this.stime === ""
                  ? new Date(1970) - 8.64e7
                  : this.stime.getTime())
            );
          }
        };
      }
    }
  },
  data() {
    return {
      stime: "",
      etime: "",
      time: "",
      format: {
        year: "YYYY",
        month: "YYYY-MM",
        week: "YYYY  WW ",
        date: "YYYY-MM-DD",
        daterange: "YYYY-MM-DD",
        datetime: "YYYY-MM-DD HH:mm:ss",
        datetimerange: "YYYY-MM-DD HH:mm:ss"
      }
    };
  },
  watch: {
    type(val) {
      if (this.stime || this.etime) {
        this.$emit("input", this.formatTime(this.stime, this.etime));
      } else if (this.time && this.time.length === 2) {
        this.$emit("input", this.formatTime(this.time[0], this.time[1]));
      } else {
        this.$emit("input", []);
      }
    },
    // XXX
    time: {
      handler(val) {
        if (this.value.join(",") !== this.time.join(",")) {
          // this.time = this.value;
          let sTime = this.$moment(
            new Date(this.time[0]),
            this.format[this.type]
          );
          let eTime = this.$moment(
            new Date(this.time[1]),
            this.format[this.type]
          );

          this.$emit("input", [sTime, eTime]);
        }
      },
      deep: true
    },
    value: {
      handler(val) {
        if (this.type === "datetimerange" || this.type === "daterange") {
          if (this.value && this.value.join(",") !== this.time.join(",")) {
            this.time = this.value;
          }
        } else {
          if (
            this.value &&
            this.value.join(",") !== this.stime + "," + this.etime
          ) {
            this.stime = new Date(this.value[0]);
            this.etime = new Date(this.value[1]);
          }
        }
      },
      deep: true
    }
  },
  methods: {
    handleStartTime() {
      let time = [];
      if (this.isPickerEnd && this.stime > this.etime) {
        this.etime = "";
      }
      let formatTime = this.formatTime(this.stime, this.etime);
      this.$emit("input", formatTime);
      this.$emit("change", formatTime);
    },
    handleEndTime() {
      let formatTime = this.formatTime(this.stime, this.etime);
      this.$emit("input", formatTime);
      this.$emit("change", formatTime);
    },
    changeFormat(format) {
      return format ? format.replace(/Y/g, "y").replace(/D/g, "d") : "";
    },
    formatTime(start, end) {
      return [
        start ? this.$moment(start, this.format[this.type]) : "",
        end ? this.$moment(end, this.format[this.type]) : ""
      ];
    }
  },
  mounted() {
    if (!(this.value && this.value[0] && this.value[1])) {
      let date = new Date();
      if (this.type === "datetimerange" || this.type === "daterange") {
        this.time = [date, date];
      } else {
        this.stime = date;
        this.etime = date;
      }
    } else {
      if (this.type === "datetimerange" || this.type === "daterange") {
        this.time = [new Date(this.value[0]), new Date(this.value[1])];
      } else {
        this.stime = new Date(this.value[0]);
        this.etime = new Date(this.value[1]);
      }
    }
  }
};
</script>
<style scoped>
.date-time {
  display: inline-block;
}
.date-time-wrap {
  display: flex;
  align-items: center;
}
.date-range-wrap {
  height: 30px;
  margin-top: 4px;
}
.date-time-input {
  flex: 1;
}

.date-time-wrap > span {
  padding: 0 5px;
}
</style>



