<template>
  <div class="panel-flex" :style="{width:width, padding:padValue}">
    <div class="" :class="{'panel-flex-1':!leftWidth}" :style="{width: leftWidth,'min-width': leftWidth, height: height}">
      <slot name="left"></slot>
    </div>
    <div class="" :class="{'panel-flex-1':!rightWidth}" :style="{width: rightWidth, height: height}">
      <slot name="right"></slot>
    </div>
    <slot></slot>
  </div>
</template>
<script>
export default {
  props: {
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "500px"
    },
    leftWidth: {
      type: String,
      default: "250px"
    },
    rightWidth: {
      type: String
    },
    padding: {
      type: String,
      default: "0"
    }
  },
  computed: {
    padValue() {
      return this.setPad(this.padding);
    }
  },
  methods: {
    setPad(padV) {
      return ["t", "r", "b", "l"]
        .map(item => {
          if (padV.indexOf(item) === -1) {
            return 0;
          } else {
            return "10px";
          }
        })
        .join(" ");
    }
  }
};
</script>

<style scoped>
.panel-flex {
  /* margin: 0 -5px; */
  display: flex;
  width: 100%;
}
.panel-flex > .mar-lr-5 {
  /* overflow: auto; */
}
.panel-flex-1 {
  flex: 1;
}
</style>



