<template>
  <!-- class="echartsHight"  -->
  <div>
    <slot name="searchTool">
    </slot>
    <el-table :ref="id" :id="id" :data="data" :show-summary="showSummary" :summary-method="summaryMethod" :border="border" v-loading.lock="isLoading" :max-height="maxHeight" :highlight-current-row="showRadio" @select="select" @select-all="selectAll" @row-click="rowClick" @row-dblclick="rowDblclick" @selection-change="selectionChange" style="width: 100%">
      <el-table-column v-if="showSelection" type="selection" header-align="center" align="center">
      </el-table-column>
      <el-table-column v-if="showIndex" type="index" label="" width="60" header-align="center" align="center">
      </el-table-column>
      <!-- :class-name="item.subStr === true?'sys_text_substr':''" -->
      <template v-for='item in colums'>
        <el-table-column :label="item.label" v-if="!item.hidden===true&&item.children" :align="item.align? item.align:'center'" header-align="center" :key="item.prop" :prop="item.prop" :class-name="item.subStr === true?'sys_text_substr':''" :min-width="item.width ? item.width:' '" :sortable="item.sortable? item.sortable:false" :show-overflow-tooltip="item.showOverflowTooltip ? item.showOverflowTooltip : false">
          <el-table-column v-for='itemChild in item.children' :label="itemChild.label" :align="itemChild.align? itemChild.align:'center'" header-align="center" :key="itemChild.prop" :prop="itemChild.prop" :class-name="itemChild.subStr === true?'sys_text_substr':''" :min-width="itemChild.width ? itemChild.width:' '" :sortable="itemChild.sortable? itemChild.sortable:false" :show-overflow-tooltip="itemChild.showOverflowTooltip ? itemChild.showOverflowTooltip : false">
            <template slot-scope="scope2">
              <el-popover class="table_popover" trigger="hover" placement="top" v-if="itemChild.popover">
                <slot name="hover">
                  <p>{{ typeof itemChild.popover === "boolean"?customDisplay(itemChild, scope2):scope2.row[itemChild.popover]||itemChild.popover }}</p>
                </slot>
                <div slot="reference" class="name-wrapper">
                  <el-tag>{{ customDisplay(itemChild, scope2) }}</el-tag>
                </div>
              </el-popover>
              <template v-if="!itemChild.popover">{{customDisplay(itemChild, scope2)}}</template>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column :label="item.label" v-if="!(item.hidden===true)&&!item.children" :align="item.align? item.align:'center'" header-align="center" :key="item.prop" :prop="item.prop" :class-name="item.subStr === true?'sys_text_substr':''" :min-width="item.width ? item.width:' '" :sortable="item.sortable? item.sortable:false" :show-overflow-tooltip="item.showOverflowTooltip ? item.showOverflowTooltip : false">
          <template slot-scope="scope">
            <el-popover class="table_popover" trigger="hover" placement="top" v-if="item.popover">
              <slot name="hover">
                <p>{{ typeof item.popover === "boolean"?customDisplay(item, scope):scope.row[item.popover]||item.popover }}</p>
              </slot>
              <div slot="reference" class="name-wrapper">
                <el-tag>{{ customDisplay(item, scope) }}</el-tag>
              </div>
            </el-popover>
            <template v-if="!item.popover">{{customDisplay(item, scope)}}</template>
          </template>
        </el-table-column>
      </template>
      <slot></slot>
      <slot name="operation">
      </slot>

    </el-table>
    <v-page v-if="isPage" :page="page" @currentChange="currentChange" @pageSizeChange="pageSizeChange" :class="isClass?'cation':' ' "></v-page>
    <!-- :class=isClass?'cation':"" -->
  </div>
</template>

<script>
/*columsformat 
   * cols,
   * label,prop,align(,),
   * width(,),
   * sortable(),
   * displace() {id1:"1",id2:"2",...}
   * format() "YYYY-MM-DD HH:mm:ss"
   * subStr(...) true() [number]()
   * popover() true() [string]()
   * customFn()
   * showSummary()  summaryMethod 
   *
   * showSelection(),,CheckBox
   * showIndex(),,
   * showRadio(),,
   * border(),,
   *
   * select(CheckBox),selectionrow,
   * selectAll(),selection,
   * rowClick(),row, event, column,
   * rowDblclick(),row, event,
   *
   * ;frontPage,setdata
   *
   * */
import vPage from "./page";
import { varType } from "../../util/util.js";
import FileSaver from "file-saver";
import XLSX from "xlsx";

export default {
  props: {
    // propData: {
    //   type: Array,
    //   default: []
    // },
    id: {
      type: String,
      default: "baseTable"
    },
    frontPage: {
      default: false
    },
    tableList: {
      type: [Object, Array]
    },
    colums: {
      type: Array,
      default() {}
    },
    searchData: {
      type: Object,
      default() {
        return {
          currentPage: 1,
          pageSize: 5
        };
      }
    },
    isClass: {
      default: false
    },
    //,
    showSelection: {
      //                type: [Boolean,String,N],
      default: false
    },
    //
    type: {
      type: String,
      default: "normal"
    },
    //,
    showIndex: {
      //                type: Boolean,
      default: true
    },
    //,
    showRadio: {
      //                type: Boolean,
      default: true
    },
    // 
    border: {
      type: Boolean,
      default: true
    },
    // maxHeight: {
    //   default() {
    //     return 500;
    //   }
    // },
    showSummary: {
      type: Boolean,
      default: false
    },
    summaryMethod: {
      type: Function
    },
    that: {
      default: null
    },
    selectAttr: {
      type: String
    }
  },
  components: {
    vPage
  },
  data() {
    // this.$nextTick(() => {
    // this.maxHeight = document.getElementsByClassName(
    //   "plat-div-content"
    // )[0].clientHeight;
    // });
    return {
      maxHeight: 500,
      isLoading: false,
      tableData: [],
      page: {
        pageSizes: 0,
        pageRange: 10,
        thisPage: 1,
        totalPage: 0
      },
      isPage: true,
      checkList: [],
      selectable: item => {
        return false;
      },
      filterList: null
    };
  },
  methods: {
    //checkbox
    select(selection, row) {
      let val = {
        selection,
        row
      };
      this.$emit("select", val);
    },
    setData(data) {
      // 
      if (varType(data) === "array") {
        this.tableData = data;
        if (this.frontPage) {
          this.isPage = true;
          this.page = {
            pageSizes: data.length,
            pageRange: this.page.pageRange,
            thisPage: this.page.thisPage,
            totalPage: Math.ceil(data.length / this.page.pageRange)
          };
        } else {
          this.isPage = false;
        }
      } else {
        if (!this.frontPage) {
          //
          this.tableData = data.records;
          let aspnetPager = data.aspnetPager;
          if (aspnetPager && aspnetPager.currentPage) {
            this.page = {
              pageSizes: aspnetPager.totalCount,
              pageRange: aspnetPager.pageSize,
              thisPage: aspnetPager.currentPage,
              totalPage: Math.ceil(
                aspnetPager.totalCount / aspnetPager.pageSize
              )
            };
          } else {
            this.isPage = false;
          }
        } else {
          //
          data = data ? data : [];
          this.tableData = data;
          this.page = {
            pageSizes: data.length,
            pageRange: this.page.pageRange,
            thisPage: this.page.thisPage,
            totalPage: Math.ceil(data.length / this.page.pageRange)
          };
        }
      }
      // 

      this.$nextTick(() => {
        if (this.showSelection && this.selectAttr) {
          // this.$refs.table.
          // console.log(this.tableData, this.$refs[this.id]);
          this.tableData.forEach(item => {
            if (item[this.selectAttr]) {
              this.$refs[this.id].toggleRowSelection(item, true);
            }
          });
        }
      });
    },
    currentChange(val) {
      if (!this.frontPage) {
        //
        this.searchData.currentPage = val;
        this.$emit("callBack", this.searchData);
      } else {
        //
        this.page.thisPage = val;
      }
    },
    pageSizeChange(size) {
      if (!this.frontPage) {
        //
        this.searchData.pageSize = size;
        this.$emit("callBack", this.searchData);
      } else {
        //
        this.page.pageRange = size;
        this.page.totalPage = Math.ceil(this.tableData.length / size);
      }
    },
    //
    selectAll(selection) {
      let val = {
        selection
      };
      this.$emit("selectAll", val);
    },
    //
    rowClick(row, event, column) {
      if (
        event.target.outerHTML.indexOf("span") == -1 ||
        event.target.outerHTML.indexOf("el-checkbox") != -1
      ) {
        this.$refs[this.id].toggleRowSelection(row);
      }
      let val = {
        row,
        event,
        column
      };
      this.$emit("rowClick", val);
    },
    //
    rowDblclick(row, event) {
      let val = {
        row: row,
        event: event
      };
      this.$emit("rowDblclick", val);
    },

    edit(val) {
      this.$emit("editTable", val);
    },
    deleteTable(val) {
      this.$emit("deleteTable", val);
    },
    format(value, format) {
      return this.$moment(value, format);
    },
    customDisplay(item, scope) {
      var temp = "";
      if (item.displace) {
        temp = item.displace[scope.row[item.prop]];
      } else {
        temp = scope.row[item.prop];
      }

      //
      if (
        item.format &&
        (typeof temp === "string" || typeof temp === "number")
      ) {
        try {
          temp = this.format(temp, item.format);
        } catch (e) {
          console.warn(e);
        }
      }

      //
      if (
        item.subStr &&
        typeof item.subStr === "number" &&
        temp.length > item.subStr
      ) {
        temp = temp.substring(0, item.subStr) + "...";
      } else if (typeof item.subStr === "boolean") {
      }

      if (varType(item.customFn) === "function") {
        temp = item.customFn(temp, scope.row);
      }
      return temp;
    },
    //
    selectionChange(selection) {
      this.checkList = selection;
      this.$emit("selection-change", selection);
    },

    //
    getCheckedRows() {
      return this.checkList;
    },
    // selected  true 
    toggleRowSelection(rows) {
      this.$each(rows, item => {
        this.$refs[this.id].toggleRowSelection(item, true);
      });
    },
    //
    clearSelection() {
      this.$refs[this.id].clearSelection();
    },
    openLoading() {
      this.isLoading = true;
    },
    closeLoading() {
      this.isLoading = false;
    },
    export(name) {
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(this.$el);
      /* get binary string as output */
      var wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array"
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          (name || "template") + ".xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    },
    filterData(query) {
      let data = this.tableData.filter(item => {
        for (let element in query) {
          // 
          if (query[element] && item[element].indexOf(query[element]) === -1) {
            return false;
          }
        }
        return true;
      });
      this.filterList = data;
      this.page = {
        pageSizes: data.length,
        pageRange: this.page.pageRange,
        thisPage: 1,
        totalPage: Math.ceil(data.length / this.page.pageRange)
      };
    }
  },
  computed: {
    data() {
      if (!this.frontPage) {
        //
        return this.tableData;
      } else {
        let data = [].concat(this.tableData);
        // 
        if (varType(this.filterList) === "array") {
          data = this.filterList;
        }

        //
        let start = (this.page.thisPage - 1) * this.page.pageRange,
          end =
            (this.page.thisPage - 1) * this.page.pageRange +
            this.page.pageRange;
        return data.slice(start, end);
      }
    }
  },
  watch: {
    tableList(val) {
      this.setData(val);
    }
  }
};
</script>

<style>
.cation {
  position: fixed;
  bottom: 25px;
  right: 0;
  width: 100%;
  padding-left: 206px;
}

.table_popover .name-wrapper {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: bottom;
  display: inline-block;
}

.table_popover .name-wrapper .el-tag {
  vertical-align: bottom;
}
.table_popover .el-tag {
  border: 0;
  background: none;
  color: #444;
  cursor: pointer;
}
</style>



