<template>
    <div>
        <el-button icon="arrow-left" @click='pre' plain></el-button>
        <el-select v-model="value" placeholder="" @change='selectChange'>
            <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
            </el-option>
        </el-select>
        <el-button icon="arrow-right" @click='next'></el-button>
    </div>
</template>

<script>
    /*:
        options,[{value:,label],elementUioptions;
        onChange,onChangevalue,;

    */
    export default {
        props: {
            options: {
                type: Array,
//       required:true
//      default(){
//        return[
//          {value:0,label:'1'}
//        ]
//      }
            }

        },
        data() {
            return {
                value: '',
                nowIndex: 0
            }
        },
        methods: {
            selectChange(val) {
                let msg = {val: val};
                this.options.forEach((value, index) => {
                    if (value['value'] == val) {
                        msg.label = value['label'];
                        msg.index = index;
                        this.nowIndex = index
                    }
                })
                this.$emit('change', msg)
            },
            pre() {
                this.nowIndex--;
                if (this.nowIndex < 0) {
                    this.nowIndex = this.options.length - 1
                }
                this.value = this.options[this.nowIndex].value
            },
            next() {
                this.nowIndex++;
                if (this.nowIndex >= this.options.length) {
                    this.nowIndex = 0
                }
                this.value = this.options[this.nowIndex].value
            }
        },
        mounted() {
            if (this.options) {
                this.value = this.options[this.nowIndex].value
            }
        }
    }
</script>

<style scoped>
    /*.el-button:focus.el-button:active{*/
    /*border: 1px solid #c4c4c4;
    color: #1f2d3d;*/
    /*}*/

</style>



