<template>
    <div class="base-div-page">
        <el-pagination @current-change="handleCurrentChange" :current-page="page.thisPage" :total="page.pageSizes" layout="slot,prev, pager, next,jumper" :page-size="pageRangeN">
            <slot>
                <div class="base-table-pageLeft">
                    {{page.pageSizes}}{{page.thisPage}}/{{page.totalPage}}
                    <span class="base-table-pagesize">
                        <input type="text" v-model.lazy="pageRange" @change="pageSizeChange" @click="showPageSizes" ref="basePageInput" class="base-input-pageSizes">
                        <ul v-show="isShowPageSizes" class="base-ul-page">
                            <li v-for="item in pageSizes" :key="item" @click="selectPage(item)">{{item}}</li>
                        </ul>
                    </span>
                    
                </div>
            </slot>
        </el-pagination>
    </div>
</template>
<script>
export default {
  props: {
    page: {
      type: Object,
      default() {
        return {
          thisPage: 1, //
          totalPage: 0, //
          pageSizes: "", //
          pageRange: "" //
        };
      }
    },
    pageSizes: {
      type: Array,
      default() {
        return ["", 100, 50, 10]; //[10, 50, 100, '']
      }
    }
  },
  data() {
    return {
      pageRange: 10,
      isShowPageSizes: false
    };
  },
  computed: {
    pageRangeN() {
      return Number(this.pageRange);
    }
  },
  methods: {
    //currentPage
    handleCurrentChange(val) {
      this.$emit("currentChange", val);
    },
    //inputpaegsizes
    pageSizeChange() {
      this.changeValue(this.pageRange);
      this.$emit("pageSizeChange", this.pageRange);
    },
    //input
    showPageSizes() {
      this.isShowPageSizes = !this.isShowPageSizes;
    },
    //
    selectPage(val) {
      if (val === "") {
        this.$refs["basePageInput"].focus();
      } else if (this.pageRange != val) {
        this.pageRange = val;
        this.$emit("pageSizeChange", this.pageRange);
      }
      this.isShowPageSizes = false;
    },
    //input
    changeValue(val) {
      if (isNaN(parseInt(val)) || parseInt(val) <= 0) {
        this.pageRange = 10;
      } else {
        this.pageRange = parseInt(val);
      }
    }
  },
  watch: {
    page: function(newV, oldV) {
      this.pageRange = newV.pageRange;
    }
  },
  mounted() {
    let me = this;
    document.addEventListener("click", function(e) {
      if (
        e.target == me.$el.getElementsByClassName("base-input-pageSizes")[0]
      ) {
        return;
      } else {
        me.isShowPageSizes = false;
      }
    });
  }
};
</script>
<style scoped>
.base-div-page {
  text-align: right;
  margin-top: 8px;
}

.base-table-pageLeft {
  float: left;
  position: relative;
  margin-left: -5px;
  line-height: 28px;
}

.el-pagination__jump {
  margin-right: -5px;
}

.base-table-pageLeft input {
  box-sizing: border-box;
  text-align: center;
  height: 26px;
  width: 50px;
  border: 1px solid #d1dbe5;
  border-radius: 2px;
}

.base-table-pageLeft ul {
  background: #fff;
  border: 1px solid #d1dbe5;
  position: absolute;
  left: 0;
  top: -101px;
  /*top: 25px;*/
  width: 50px;
  z-index: 999;
}

.base-table-pageLeft li {
  list-style: none;
  line-height: 25px;
  height: 25px;
  text-align: center;
  cursor: pointer;
}

.base-table-pageLeft li:hover {
  background: #e4e8f1;
}
.base-table-pagesize {
  position: relative;
}
</style>


